import { Direction, Directionality } from '@angular/cdk/bidi';
import { Directive, ElementRef, Input, OnChanges, OnDestroy, Optional, Renderer2, SimpleChange, SimpleChanges } from '@angular/core';
import { ThemeType } from '@ant-design/icons-angular';

import { NzConfigKey, NzConfigService, WithConfig } from 'ng-zorro-antd/core/config';
import { BooleanInput, InputObservable } from 'ng-zorro-antd/core/types';
import { InputBoolean } from 'ng-zorro-antd/core/util';
import { Observable, Subject } from 'rxjs';
import { filter, map, takeUntil } from 'rxjs/operators';

const NZ_CONFIG_MODULE_NAME: NzConfigKey = 'form';

export type NzFormLayoutType = 'horizontal' | 'vertical' | 'inline';

export const DefaultTooltipIcon = {
  type: 'question-circle',
  theme: 'outline'
} as const;

@Directive({
  selector: '[cmacs-form]',
  exportAs: 'cmacsForm',
  host: {
    '[class.ant-form-horizontal]': `cmacsLayout === 'horizontal'`,
    '[class.ant-form-vertical]': `cmacsLayout === 'vertical'`,
    '[class.ant-form-inline]': `cmacsLayout === 'inline'`,
    '[class.ant-form-rtl]': `dir === 'rtl'`
  }
})
export class CmacsFormDirective implements OnChanges, OnDestroy, InputObservable {
  readonly _nzModuleName: NzConfigKey = NZ_CONFIG_MODULE_NAME;

  @Input() cmacsLayout: NzFormLayoutType = 'horizontal';
  @Input() @WithConfig() @InputBoolean() cmacsNoColon: boolean = false;
  @Input() @WithConfig() cmacsAutoTips: Record<string, Record<string, string>> = {};
  @Input() @InputBoolean() cmacsDisableAutoTips = false;
  @Input() @WithConfig() cmacsTooltipIcon: string | { type: string; theme: ThemeType } = DefaultTooltipIcon;

  dir: Direction = 'ltr';
  destroy$ = new Subject();
  private inputChanges$ = new Subject<SimpleChanges>();

  getInputObservable<K extends keyof this>(changeType: K): Observable<SimpleChange> {
    return this.inputChanges$.pipe(
      filter(changes => changeType in changes),
      map(value => value[changeType as string])
    );
  }

  constructor(
    public nzConfigService: NzConfigService,
    elementRef: ElementRef,
    private renderer: Renderer2,
    @Optional() private directionality: Directionality
  ) {
    this.renderer.addClass(elementRef.nativeElement, 'ant-form');

    this.dir = this.directionality.value;
    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.inputChanges$.next(changes);
  }

  ngOnDestroy(): void {
    this.inputChanges$.complete();
    this.destroy$.next();
    this.destroy$.complete();
  }
}
