import { AfterViewInit, Component, ElementRef, Input, OnChanges, OnInit, Renderer2, TemplateRef, ViewChild } from '@angular/core';
import { KPI } from '../cmacs-kpi/cmacs-kpi.component';
import { CeldType } from '../core/enums/CeldType.enum';
import { GridConfig } from '../core/interfaces/grid-config';

interface RowInput {
  id: string;
  label: string;
  values: number[];
  isChild: boolean;
  children: RowInput[];
  color: string;
}

interface ColumnInput {
  name: string;
  nameKpi: string;
  hasKpi: boolean;
}


@Component({
  selector: 'cmacs-kpi-vs',
  templateUrl: './cmacs-kpi-vs.component.html',
  styleUrls: ['./cmacs-kpi-vs.component.css']
})
export class CmacsKpiVsComponent implements OnInit, OnChanges, AfterViewInit {

  @ViewChild('columnTemplate', { read: TemplateRef, static: false }) columnTemplate: TemplateRef<{}>;

  // content
  @Input() view!: number[];
  @Input() data!: { columns: ColumnInput[], rows: RowInput[] };

  // table
  dataTable!: any[];
  configuration!: GridConfig;

  scrollY = 200;
  scroll = { x: '434px', y: this.scrollY + 'px' };
  p = 1;

  col2 = 150;
  col3 = 70;
  loading = true;

  // chart
  showChart = false;
  minWidth = 434;
  listKpi: KPI[][] = [];
  kpiColumns: ColumnInput[];
  colorsScheme: string[] = [];

  constructor(
    private renderer: Renderer2,
    private elementRef: ElementRef,
  ) { }

  ngOnInit(): void {
    this.fillKPI();
  }

  ngOnChanges(): void {
    this.setScroll();

    this.p = 1;
    if (this.view && this.view.length === 2) {
      this.p = this.view[0] > this.minWidth ? this.view[0] / this.minWidth : 1;
      if (this.renderer) {
        this.resize();
      }
    }
    setTimeout(() => {
      this.setConfiguration();
      this.setData();
    }, 0);
  }

  setScroll(): void {
    let p = 1;
    if (this.view && this.view.length === 2) {
      p = this.view[1] * 0.5 > this.scrollY ? this.view[1] * 0.5 / this.scrollY : 1;
    }
    this.scroll = { x: '300px', y: this.scrollY * p + 'px' };
  }

  resize() {
    const width = this.view[0];
    this.renderer.setStyle(this.elementRef.nativeElement, 'width', `${width}px`);
  }

  ngAfterViewInit(): void {
    this.resize();
  }

  fillKPI(): void {
    this.colorsScheme = this.data.rows.map(x => x.color);
    this.kpiColumns = this.data.columns.filter(d => d.hasKpi);
    this.kpiColumns.forEach((c, index) => {
      this.listKpi.push(this.data.rows.map(r => ({ count: r.values[index], description: '' } as KPI)));
    });
  }

  setConfiguration(): void {
    this.configuration = {
      // column with dot
      fields: [
        {
          celdType: CeldType.TemplateRef,
          display: '',
          property: 'color',
          editTemplate: 3,
          width: '70px',
          minWidth: 70,
          maxWidth: 120,
          resizable: true
        },
      ],
      fieldId: 'key',
      fieldRate: 'fav'
    };

    this.data.columns.forEach((x, index) => {
      const colWidth = (x.hasKpi ? this.col3 : this.col2);
      this.configuration.fields.push({
        celdType: CeldType.Default,
        editTemplate: 3,
        display: x.name,
        property: x.hasKpi ? `prop${index}` : 'name',
        editable: false,
        width: colWidth * this.p + 'px',
        minWidth: colWidth * this.p - 10,
        maxWidth: colWidth * this.p + 100,
        resizable: true,
      });
    });
  }

  setData(): void {
    this.loading = true;
    this.dataTable = [];
    this.data.rows.forEach(x => {
      this.dataTable.push(this.getItemTable(x));
    });
    setTimeout(() => {
      this.loading = false;
    }, 0);
  }

  getItemTable(x: RowInput): any {
    const newItem: any = {
      key: x.id,
      color: {
        ref: this.columnTemplate,
        context: {
          color: x.color
        }
      },
      name: x.label
    };
    if (x.children.length > 0 || !x.isChild) {
      newItem.children = x.children.map(e => this.getItemTable(e));
    }
    x.values.forEach((v, i) => {
      newItem[`prop${i + 1}`] = v.toFixed(2);
    });
    return newItem;
  }
}
