import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'cmacs-button-favorite',
  templateUrl: './cmacs-button-favorite.component.html',
  styleUrls: ['./cmacs-button-favorite.component.css']
})
export class CmacsButtonFavoriteComponent implements OnInit {
  @Input() value: boolean;
  @Output() onChangedValue: EventEmitter<boolean> = new EventEmitter<boolean>();
  iconFavorites:string = '';

  constructor() { }

  ngOnInit(): void {
  }

  changedValue(){
    this.value = this.value? false:true;
    this.onChangedValue.emit(this.value);
  }

}
