import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Input,
  OnDestroy,
  Optional,
  Renderer2,
  SkipSelf,
  ViewEncapsulation
} from '@angular/core';
import { ThemeType } from '@ant-design/icons-angular';
import { BooleanInput, NzTSType } from 'ng-zorro-antd/core/types';

import { InputBoolean, toBoolean } from 'ng-zorro-antd/core/util';
import { Subject } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';
import { DefaultTooltipIcon, CmacsFormDirective } from './form.directive';

export interface NzFormTooltipIcon {
  type: NzTSType;
  theme: ThemeType;
}

function toTooltipIcon(value: string | NzFormTooltipIcon): Required<NzFormTooltipIcon> {
  const icon = typeof value === 'string' ? { type: value } : value;
  return { ...DefaultTooltipIcon, ...icon };
}

@Component({
  selector: 'cmacs-form-label',
  exportAs: 'cmacsFormLabel',
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <label [attr.for]="cmacsFor" [class.ant-form-item-no-colon]="cmacsNoColon" [class.ant-form-item-required]="cmacsRequired">
      <ng-content></ng-content>
      <span *ngIf="cmacsTooltipTitle" class="ant-form-item-tooltip" nz-tooltip [nzTooltipTitle]="cmacsTooltipTitle">
        <ng-container *nzStringTemplateOutlet="tooltipIcon.type; let tooltipIconType">
          <i nz-icon [nzType]="tooltipIconType" [nzTheme]="tooltipIcon.theme"></i>
        </ng-container>
      </span>
    </label>
  `
})
export class CmacsFormLabelComponent implements OnDestroy {

  @Input() cmacsFor?: string;
  @Input() @InputBoolean() cmacsRequired = false;
  @Input()
  set cmacsNoColon(value: boolean) {
    this.noColon = toBoolean(value);
  }
  get cmacsNoColon(): boolean {
    return this.noColon !== 'default' ? this.noColon : this.CmacsFormDirective?.cmacsNoColon;
  }

  private noColon: boolean | 'default' = 'default';

  @Input() cmacsTooltipTitle?: NzTSType;
  @Input()
  set cmacsTooltipIcon(value: string | NzFormTooltipIcon) {
    this._tooltipIcon = toTooltipIcon(value);
  }
  // due to 'get' and 'set' accessor must have the same type, so it was renamed to `tooltipIcon`
  get tooltipIcon(): NzFormTooltipIcon {
    return this._tooltipIcon !== 'default' ? this._tooltipIcon : toTooltipIcon(this.CmacsFormDirective?.cmacsTooltipIcon || DefaultTooltipIcon);
  }
  private _tooltipIcon: NzFormTooltipIcon | 'default' = 'default';

  private destroy$ = new Subject();

  constructor(
    elementRef: ElementRef,
    renderer: Renderer2,
    private cdr: ChangeDetectorRef,
    @Optional() @SkipSelf() private CmacsFormDirective: CmacsFormDirective
  ) {
    renderer.addClass(elementRef.nativeElement, 'ant-form-item-label');

    if (this.CmacsFormDirective) {
      this.CmacsFormDirective
        .getInputObservable('cmacsNoColon')
        .pipe(
          filter(() => this.noColon === 'default'),
          takeUntil(this.destroy$)
        )
        .subscribe(() => this.cdr.markForCheck());

      this.CmacsFormDirective
        .getInputObservable('cmacsTooltipIcon')
        .pipe(
          filter(() => this._tooltipIcon === 'default'),
          takeUntil(this.destroy$)
        )
        .subscribe(() => this.cdr.markForCheck());
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
