  @Input() rateCount = 5;
  @Input() @InputBoolean() multiSelect = false;
  @Output() sortChange = new EventEmitter<any>();
  @Output() filterChange = new EventEmitter<any>();
  @Output() onrowdeleted = new EventEmitter<any>();
  @Output() onrowadded = new EventEmitter<any>();
