import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ComponentFactory,
  ComponentFactoryResolver,
  Directive,
  ElementRef,
  EventEmitter,
  Host,
  Input,
  Optional,
  Output,
  Renderer2,
  ViewContainerRef,
  ViewEncapsulation
} from '@angular/core';
import { zoomBigMotion } from 'ng-zorro-antd/core/animation';
import { isPresetColor, NzPresetColor } from 'ng-zorro-antd/core/color';
import { NzNoAnimationDirective } from 'ng-zorro-antd/core/no-animation';
import { NgStyleInterface, NzTSType } from 'ng-zorro-antd/core/types';

import { Directionality } from '@angular/cdk/bidi';
import { isTooltipEmpty, CmacsTooltipBaseComponent, NzTooltipBaseDirective, NzTooltipTrigger, PropertyMapping } from './base';

@Directive({
  selector: '[cmacs-tooltip]',
  exportAs: 'cmacsTooltip',
  host: {
    '[class.ant-tooltip-open]': 'visible'
  }
})
export class CmacsTooltipDirective extends NzTooltipBaseDirective {
  @Input('cmacsTooltipTitle') title?: NzTSType | null;
  @Input('cmacs-tooltip') directiveTitle?: NzTSType | null;
  @Input('cmacsTooltipTrigger') trigger?: NzTooltipTrigger = 'hover';
  @Input('cmacsTooltipPlacement') placement?: string | string[] = 'top';
  @Input('cmacsTooltipOrigin') origin?: ElementRef<HTMLElement>;
  @Input('cmacsTooltipVisible') visible?: boolean;
  @Input('cmacsTooltipMouseEnterDelay') mouseEnterDelay?: number;
  @Input('cmacsTooltipMouseLeaveDelay') mouseLeaveDelay?: number;
  @Input('cmacsTooltipOverlayClassName') overlayClassName?: string;
  @Input('cmacsTooltipOverlayStyle') overlayStyle?: NgStyleInterface;
  @Input() cmacsTooltipColor?: string;

  // tslint:disable-next-line:no-output-rename
  @Output('cmacsTooltipVisibleChange') readonly visibleChange = new EventEmitter<boolean>();

  componentFactory: ComponentFactory<CmacsToolTipComponent> = this.resolver.resolveComponentFactory(CmacsToolTipComponent);

  constructor(
    elementRef: ElementRef,
    hostView: ViewContainerRef,
    resolver: ComponentFactoryResolver,
    renderer: Renderer2,
    @Host() @Optional() noAnimation?: NzNoAnimationDirective
  ) {
    super(elementRef, hostView, resolver, renderer, noAnimation);
  }

  protected getProxyPropertyMap(): PropertyMapping {
    return {
      cmacsTooltipColor: ['cmacsColor', () => this.cmacsTooltipColor]
    };
  }
}

@Component({
  selector: 'cmacs-tooltip',
  exportAs: 'cmacsTooltipComponent',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  animations: [zoomBigMotion],
  styleUrls: ['./cmacs-tooltip.component.css'],
  template: `
    <ng-template
      #overlay="cdkConnectedOverlay"
      cdkConnectedOverlay
      nzConnectedOverlay
      [cdkConnectedOverlayOrigin]="origin"
      [cdkConnectedOverlayOpen]="_visible"
      [cdkConnectedOverlayPositions]="_positions"
      [cdkConnectedOverlayPush]="true"
      (overlayOutsideClick)="onClickOutside($event)"
      (detach)="hide()"
      (positionChange)="onPositionChange($event)"
    >
      <div
        class="ant-tooltip"
        [class.ant-tooltip-rtl]="dir === 'rtl'"
        [ngClass]="_classMap"
        [ngStyle]="cmacsOverlayStyle"
        [@.disabled]="noAnimation?.nzNoAnimation"
        [nzNoAnimation]="noAnimation?.nzNoAnimation"
        [@zoomBigMotion]="'active'"
      >
        <div class="ant-tooltip-content">
          <div class="ant-tooltip-arrow">
            <span class="ant-tooltip-arrow-content" [ngStyle]="_contentStyleMap"></span>
          </div>
          <div class="ant-tooltip-inner" [ngStyle]="_contentStyleMap">
            <ng-container *nzStringTemplateOutlet="cmacsTitle">{{ cmacsTitle }}</ng-container>
          </div>
        </div>
      </div>
    </ng-template>
  `,
  preserveWhitespaces: false
})
export class CmacsToolTipComponent extends CmacsTooltipBaseComponent {
  cmacsTitle: NzTSType | null = null;

  cmacsColor?: string | NzPresetColor;

  _contentStyleMap: NgStyleInterface = {};

  constructor(
    cdr: ChangeDetectorRef,
    @Optional() directionality: Directionality,
    @Host() @Optional() public noAnimation?: NzNoAnimationDirective
  ) {
    super(cdr, directionality, noAnimation);
  }

  protected isEmpty(): boolean {
    return isTooltipEmpty(this.cmacsTitle);
  }

  updateStyles(): void {
    const isColorPreset = this.cmacsColor && isPresetColor(this.cmacsColor);

    this._classMap = {
      [this.cmacsOverlayClassName]: true,
      [`${this._prefix}-placement-${this.preferredPlacement}`]: true,
      [`${this._prefix}-${this.cmacsColor}`]: isColorPreset
    };

    this._contentStyleMap = {
      backgroundColor: !!this.cmacsColor && !isColorPreset ? this.cmacsColor : null
    };
  }
}
