import {
  ChangeDetectionStrategy,
  Component,
  ContentChild,
  EventEmitter,
  Inject,
  InjectionToken,
  Input,
  OnChanges,
  OnDestroy,
  Output,
  SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { TabTemplateContext } from './interfaces';

import { Subject } from 'rxjs';

import { BooleanInput, NzSafeAny } from 'ng-zorro-antd/core/types';
import { InputBoolean } from 'ng-zorro-antd/core/util';

import { CmacsTabLinkDirective, CmacsTabLinkTemplateDirective } from './tab-link.directive';
import { CmacsTabDirective } from './tab.directive';

/**
 * Used to provide a tab set to a tab without causing a circular dependency.
 */
export const NZ_TAB_SET = new InjectionToken<NzSafeAny>('NZ_TAB_SET');

@Component({
  selector: 'cmacs-tab',
  exportAs: 'cmacsTab',
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./tab.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <ng-template #tabLinkTemplate>
      <ng-content select="[cmacs-tab-link]"></ng-content>
    </ng-template>
    <ng-template #contentTemplate><ng-content></ng-content></ng-template>
  `
})
export class CmacsTabComponent implements OnChanges, OnDestroy {

  @Input() cmacsTitle: string | TemplateRef<TabTemplateContext> = '';
  @Input() cmacsType = 'icon';
  @Input() @InputBoolean() closable = false;
  @Input() closeIcon: string | TemplateRef<NzSafeAny> = 'close';
  @Input() @InputBoolean() disabled = false;
  @Input() @InputBoolean() forceRender = false;
  @Output() readonly select = new EventEmitter<void>();
  @Output() readonly deselect = new EventEmitter<void>();
  @Output() readonly cmacsClick = new EventEmitter<void>();
  @Output() readonly cmacsContextmenu = new EventEmitter<MouseEvent>();

  @ContentChild(CmacsTabLinkTemplateDirective, { static: false }) nzTabLinkTemplateDirective!: CmacsTabLinkTemplateDirective;
  @ContentChild(CmacsTabDirective, { static: false, read: TemplateRef }) template: TemplateRef<void> | null = null;
  @ContentChild(CmacsTabLinkDirective, { static: false }) linkDirective!: CmacsTabLinkDirective;
  @ViewChild('contentTemplate', { static: true }) contentTemplate!: TemplateRef<NzSafeAny>;

  isActive: boolean = false;
  position: number | null = null;
  origin: number | null = null;
  readonly stateChanges = new Subject<void>();

  get content(): TemplateRef<NzSafeAny> {
    return this.template || this.contentTemplate;
  }

  get label(): string | TemplateRef<NzSafeAny> {
    return this.cmacsTitle || this.nzTabLinkTemplateDirective?.templateRef;
  }

  constructor(@Inject(NZ_TAB_SET) public closestTabSet: NzSafeAny) {}

  ngOnChanges(changes: SimpleChanges): void {
    const { cmacsTitle, disabled, forceRender } = changes;
    if (cmacsTitle || disabled || forceRender) {
      this.stateChanges.next();
    }
  }

  ngOnDestroy(): void {
    this.stateChanges.complete();
  }
}
