import { Injectable } from '@angular/core';
import html2canvas from 'html2canvas';
import * as moment_ from 'moment';
import 'moment/locale/en-ie';
import { ExportAsService, ExportAsConfig } from 'ngx-export-as';
const moment = moment_;
import jsPDF from 'jspdf';
import { Subject } from 'rxjs';
import { CeldType } from '../enums/CeldType.enum';
import { TemplateType } from '../enums/TemplateType.enum';
import { NzI18nService } from 'ng-zorro-antd/i18n';

export const ROBOTO = "";
export const ROBOTO_BOLD = "";

@Injectable({ providedIn: 'root' })
export class UtilService {

  fileName = '';
  elemID = '';
  exportCompanyName = 'Company Name';
  exportUserName = 'User Name';
  exportTitle = '';
  exportSubtitle = [];
  exportCompanyLogoUrl = 'assets/PToB_logo.png';
  exportTableCustomWidth = null;
  exportTableCustomHeight = null;
  exportCompanyLogoConfig = null;
  exportTitleConfig = null;
  pageBreakTitle = null;
  exportNewTitle = '';

  private _exportCompleted: Subject<any> = new Subject<any>();
  public exportCompleted = this._exportCompleted.asObservable();

  private cmacsPdfImages = {
    checkboxEmpty: 'data:image/png;base64,',
    checkboxIndeterminate: 'data:image/png;base64,',
    checkboxSelected: 'data:image/png;base64,',
    arrowDown: 'data:image/svg+xml;base64,'
  };

  constructor(private exportAsService: ExportAsService,
    private i18n: NzI18nService) {
      
  }

  uuidv4() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, (c) => {
      // tslint:disable-next-line: no-bitwise
      const r = Math.random() * 16 | 0;

      // tslint:disable-next-line: triple-equals
      const v = c == 'x' ?
        // tslint:disable-next-line: no-bitwise
        r : (r & 0x3 | 0x8);
      return v.toString(16);
    });
  }

  get_tex_size(txt, font): any {
    const element = document.createElement('canvas');
    const context = element.getContext('2d');
    context.font = font;
    const tsize = {
      width: context.measureText(txt).width,
      // tslint:disable-next-line: radix
      height: parseInt(context.font)
    };
    return tsize;
  }

  exportTable(
    format = 'png',
    fileName,
    elemID,
    exportCompanyName = 'Company Name',
    exportUserName = 'User Name',
    exportTitle = '',
    exportCompanyLogoUrl = 'assets/PToB_logo.png',
    exportTableCustomWidth = null,
    exportTableCustomHeight = null) {

    this.fileName = fileName;
    this.elemID = elemID;
    this.exportCompanyName = exportCompanyName;
    this.exportUserName = exportUserName;
    this.exportTitle = exportTitle;
    this.exportCompanyLogoUrl = exportCompanyLogoUrl;
    this.exportTableCustomWidth = exportTableCustomWidth;
    this.exportTableCustomHeight = exportTableCustomHeight;

    const tables = document.getElementById(elemID).getElementsByTagName('table');
    this.getTableCapture(tables, format);

  }

  initExportToPdf(imgData) {
    const getLogo = () => {
      const canvasL = document.createElement('canvas');
      const ctx = canvasL.getContext('2d');
      canvasL.height = imgLogo.height;
      canvasL.width = imgLogo.width;
      ctx.drawImage(imgLogo, 0, 0);
      const logo = canvasL.toDataURL('image/PNG');
      this.saveImageToPdf(logo, imgData);
    };

    const imgLogo = new Image();
    imgLogo.crossOrigin = "Anonymous";
    imgLogo.onload = getLogo;
    imgLogo.src = this.exportCompanyLogoUrl;
  }

  saveImageToPdf(logoData, imgData) {
    const cutImageUp = () => {
      const doc = new jsPDF('l', 'mm', 'a4', true);
      const imgProps = (<any>doc).getImageProperties(imgData);
      const cuts = Math.trunc(imgProps.height / 2080) + 1;
      const pdfWidth = this.exportTableCustomWidth ? this.exportTableCustomWidth : doc.internal.pageSize.getWidth() - 2 * 15;

      let pdfHeight = (1500 * pdfWidth) / imgProps.width;
      if (this.exportTableCustomHeight) {
        pdfHeight = (this.exportTableCustomHeight * pdfWidth) / imgProps.width;
      }
      for (let y = 0; y < cuts; y++) {
        const canvas = document.createElement('canvas');
        canvas.width = imgProps.width;
        canvas.height = 2080;
        const context = canvas.getContext('2d');
        context.drawImage(image, 0, y * 2080, imgProps.width, 2080, 0, 0, canvas.width, canvas.height);
        const img = canvas.toDataURL("image/PNG");
        if (y) {
          doc.addPage();
        }
        doc.addImage(img, 'PNG', 15, 35, pdfWidth, pdfHeight, undefined, 'FAST');
      }

      this.addFooters(doc, logoData);
      const filenameFormatted = moment().format("DD.MM.YYYY.HH.mm.ss") + '_' + this.fileName + '.pdf';
      this._exportCompleted.next(true);
      doc.save(filenameFormatted);
    }

    const image = new Image();
    image.onload = cutImageUp;
    image.src = imgData;
  }

  addFooters(doc, logo, imgLogo: HTMLImageElement = null) {
    const pageCount = doc.internal.getNumberOfPages();
    const date = this.i18n.getLocale().locale !== 'de' ? moment().format('MMMM DD, YYYY') : moment().locale('DE-DE').format('DD. MMM YYYY');
    doc.setFont('Roboto')
    doc.setTextColor(151, 160, 174);
    doc.setFontSize(8);
    for (let i = 1; i <= pageCount; i++) {
      doc.setPage(i);
      doc.text(this.exportCompanyName, 15, 8, {
        align: 'left'
      });
      doc.text(this.exportUserName, doc.internal.pageSize.width - 15, 8, {
        align: 'right'
      });
      const dim = this.scaleDimension({ width: this.pxToMm(imgLogo.width), height: this.pxToMm(imgLogo.height) });
      doc.addImage(
        logo, 'PNG',
        this.exportCompanyLogoConfig && this.exportCompanyLogoConfig.x ? this.exportCompanyLogoConfig.x : 15,
        this.exportCompanyLogoConfig && this.exportCompanyLogoConfig.y ? this.exportCompanyLogoConfig.y : 14,
        this.exportCompanyLogoUrl !== 'assets/PToB_logo.png' ? dim.width : 40,
        this.exportCompanyLogoUrl !== 'assets/PToB_logo.png' ? dim.height : 5,
        undefined, 'FAST'
      );
      doc.setFontSize(9);
      doc.setTextColor(59, 63, 70);
      if (this.pageBreakTitle && i >= this.pageBreakTitle) {
        doc.text(
          this.exportNewTitle,
          this.exportTitleConfig && this.exportTitleConfig.x ? this.exportTitleConfig.x : 15,
          this.exportCompanyLogoUrl !== 'assets/PToB_logo.png' ? dim.height + 20 : 30,
          {
            align: 'left'
          }
        );
      } else {
        doc.text(
          this.exportTitle,
          this.exportTitleConfig && this.exportTitleConfig.x ? this.exportTitleConfig.x : 15,
          this.exportCompanyLogoUrl !== 'assets/PToB_logo.png' ? dim.height + 20 : 30,
          {
            align: 'left'
          }
        );
      }
      
      doc.setFontSize(8);
      if (this.exportSubtitle!!) {
        for (let j = 0; j < this.exportSubtitle.length; j++) {
          const text = this.exportSubtitle[j];
          doc.setTextColor(...text.color);
          doc.text(text.text, text.x, this.exportCompanyLogoUrl !== 'assets/PToB_logo.png' ? dim.height + 27 : text.y);
        }
      }
      doc.setFontSize(8);
      doc.setFont('Roboto', 'normal');
      doc.setTextColor(101, 108, 121);
      doc.text(this.i18n.getLocale().locale === 'en' ? 'Page ' + String(i) + ' of ' + String(pageCount) : 'Seite ' + String(i) + ' von ' + String(pageCount), 15, doc.internal.pageSize.height - 10, {
        align: 'left'
      });
      doc.text(date, doc.internal.pageSize.width - 15, doc.internal.pageSize.height - 10, {
        align: 'right'
      })
    }
    this.exportNewTitle = '';
    this.pageBreakTitle = null;
  }

  customizeCell(doc, cellCustomized) {
    if (cellCustomized.color!!) {
      doc.setTextColor(...cellCustomized.color);
    }
    if (cellCustomized.backgroundColor!!) {
      doc.setFillColor(...cellCustomized.backgroundColor);
    }
  }

  setInlineStyles(targetElem: HTMLElement | SVGElement) {
    const transformProperties = [
      'fill',
      'color',
      'font-size',
      'stroke',
      'font'
    ];

    let svgElems = Array.from(targetElem.getElementsByTagName("svg"));
    const recurseElementChildren = (node: SVGSVGElement | HTMLElement) => {
      if (!node.style)
        return;
      let styles = getComputedStyle(node);
      for (let transformProperty of transformProperties) {
        node.style[transformProperty] = styles[transformProperty];
      }
      for (let child of Array.from(node.childNodes)) {
        recurseElementChildren(child as SVGSVGElement);
      }
    }
    for (let svgElement of svgElems) {
      recurseElementChildren(svgElement);
    }
  }

  getTableCapture(tables, format) {
    tables[0].id = this.elemID + '-table-header';
    this.setInlineStyles(tables[0]);

    const options = {
      background: 'white',
      scale: 3,
      allowTaint: true,
      scrollY: -window.scrollY,
      imageTimeout: 0,
      useCORS: true
    };

    const exportAsConfig: ExportAsConfig = {
      type: 'png', // the type you want to download
      elementIdOrContent: tables[0].id, // the id of html/table element
      options
    };
    const interval = setInterval(() => {
      // tslint:disable-next-line: max-line-length
      this.exportAsService.get(exportAsConfig).subscribe(header => {

        if (tables.length > 1) {
          tables[1].id = this.elemID + '-table-body';
          this.setInlineStyles(tables[1]);

          const exportAsConfig: ExportAsConfig = {
            type: 'png', // the type you want to download
            elementIdOrContent: tables[1].id, // the id of html/table element
            options
          };
          this.exportAsService.get(exportAsConfig).subscribe(body => {
            this.combineTwoImages(header, body, format);
          });
        } else {

          const logDimensions = () => {
            console.log('Image Width', img.width, 'Image Height', img.height);
          };
          const img = new Image();
          img.onload = logDimensions;
          img.src = header;

          if (format === 'png') {
            this.exportAsService.contentToBlob(header).subscribe(blob => {
              const filenameFormatted = moment().format("DD.MM.YYYY.HH.mm.ss") + '_' + this.fileName + '.png';
              this.exportAsService.downloadFromBlob(blob, filenameFormatted);
              this._exportCompleted.next(true);
            });
          } else {
            this.initExportToPdf(header);
          }
        }

      });
      clearInterval(interval);
    }, 100);
  }

  imagesLoaded = 0;
  combineTwoImages(img1, img2, format) {
    const mergeImages = (event) => {
      this.imagesLoaded++;
      if (this.imagesLoaded < 2) {
        return;
      }
      this.imagesLoaded = 0;
      const canvas = document.createElement('canvas');
      canvas.width = image1.width;
      canvas.height = image1.height + image2.height;
      const context = canvas.getContext('2d');
      context.drawImage(image1, 0, 0, image1.width, image1.height);
      context.drawImage(image2, 0, image1.height, image2.width, image2.height);
      const combined = canvas.toDataURL('image/png');
      console.log('Image Width', canvas.width, 'Image Height', canvas.height);

      if (format === 'png') {
        this.exportAsService.contentToBlob(combined).subscribe(blob => {
          const filenameFormatted = moment().format("DD.MM.YYYY.HH.mm.ss") + '_' + this.fileName + '.png';
          this.exportAsService.downloadFromBlob(blob, filenameFormatted);
          this._exportCompleted.next(true);
        });
      } else {
        this.initExportToPdf(combined);
      }
    }

    const image1 = new Image();
    image1.onload = mergeImages;
    image1.src = img1;

    const image2 = new Image();
    image2.onload = mergeImages;
    image2.src = img2;
  }

  images = [];

  // Export table version 2
  exportTablev2(
    exportConfig
    ) {

    const doc = new jsPDF(!!exportConfig.orientation ? exportConfig.orientation:'l', !!exportConfig.unit ? exportConfig.unit:'mm', !!exportConfig.format ? exportConfig.format:'a4', true);

    this.exportCompanyLogoUrl = !!exportConfig.exportCompanyLogoUrl ? exportConfig.exportCompanyLogoUrl : 'assets/PToB_logo.png';
    this.exportCompanyName = !!exportConfig.exportCompanyName ? exportConfig.exportCompanyName : '';
    this.exportUserName = !!exportConfig.exportUserName ? exportConfig.exportUserName : '';
    this.fileName = !!exportConfig.fileName ? exportConfig.fileName : '';
    this.exportTitle = !!exportConfig.exportTitle ? exportConfig.exportTitle : '';
    this.exportSubtitle = !!exportConfig.exportSubtitle && exportConfig.exportSubtitle.length ? exportConfig.exportSubtitle : [];
    this.exportCompanyLogoConfig = !!exportConfig.exportCompanyLogoConfig ? exportConfig.exportCompanyLogoConfig : null;
    this.exportTitleConfig = !!exportConfig.exportTitleConfig ? exportConfig.exportTitleConfig : null;

    /* Adding Roboto Font */
    doc.addFileToVFS('Roboto-Regular.ttf', ROBOTO);
    doc.addFileToVFS('Roboto-Bold.ttf', ROBOTO_BOLD);
    doc.addFont('Roboto-Regular.ttf', 'Roboto', 'normal');
    doc.addFont('Roboto-Bold.ttf', 'Roboto', 'bold');
    doc.setFont('Roboto')

    if (exportConfig.legend) {
      this.drawLegendTable(doc, exportConfig.legend);
    }

    if (exportConfig.bigImage!!) {
      this.exportBigImage(doc, exportConfig.bigImage);
    }

    /* Add custom images */
    if (exportConfig.customImages!!) {
      this.addCustomImages(doc, exportConfig.customImages);
    }

    if (!exportConfig.hideTable) {
      if (exportConfig.exportMutiple) {
        for (let item of exportConfig.exportMultipleData) {
          let finalY = null;
          if (item.newPage) {
            doc.addPage();
          } else {
            finalY = item.startY !== null && item.startY !== undefined ? 0 + item.startY : null;
          }
          if (item.newTitle) {
            this.pageBreakTitle = doc.internal.pages.length;
            this.exportNewTitle = item.newTitle;
          }
          this.drawTableContent(doc, item, finalY);
        }
      } else {
        this.drawTableContent(doc, exportConfig);
      }    
    }

    if (this.images.length) {
      this.renderTemplate(doc, exportConfig.data);
    } else {
      this.exportToPdfV2(doc);
    }
    
  }

  exportBigImage(doc, bigImage: any) {
    const cutImageUp = () => {
      const cuts = Math.trunc(image.naturalHeight / bigImage.pdfHeight) + 1;
      const pdfWidth = doc.internal.pageSize.getWidth() - 2 * 15;
      const imageWidth = bigImage.width >= pdfWidth ? pdfWidth : bigImage.width;

      for (let y = 0; y < cuts; y++) {
        const canvas = document.createElement('canvas');
        canvas.width = image.naturalWidth;
        canvas.height = bigImage.pdfHeight;
        const context = canvas.getContext('2d');
        context!.drawImage(image, 0, y * bigImage.pdfHeight, image.naturalWidth, bigImage.pdfHeight, 0, 0, canvas.width, canvas.height);
        const img = canvas.toDataURL("image/PNG");
        if (y) {
          doc.addPage();
        }
        doc.addImage(img, 'PNG', 15, bigImage.top, imageWidth, bigImage.height, undefined, 'FAST');
      }
    }

    const image = new Image();
    image.onload = cutImageUp;
    image.src = bigImage.src;
  }

  drawTableContent(doc, exportConfig, finalY = null) {
    const columns = exportConfig.columns!! ? exportConfig.columns : this.getColumns(exportConfig);
    const rows = exportConfig.rows!! ? exportConfig.rows : this.getRows(exportConfig);
    doc.autoTable({
      head: exportConfig.columns!! ? null : [columns],
      columns: exportConfig.columns!! ? columns : null,
      body: rows,
      rowPageBreak: 'avoid',
      theme: 'plain',
      showHead: exportConfig.hideHeader !== null && exportConfig.hideHeader !== undefined && exportConfig.hideHeader ? 'never' : 'everyPage',
      headStyles: exportConfig.headStyles ?
        exportConfig.headStyles :
        {
          font: 'Roboto',
          fontStyle: 'normal',
          fillColor: '#f6f7fb',
          textColor: '#656c79',
          fontSize: 9,
      },
      bodyStyles: {
        font: 'Roboto',
        fontStyle: 'normal',
        fillColor: '#ffffff',
        textColor: '#97a0ae',
        fontSize: 9
      },
      columnStyles: exportConfig.columnStyles,
      tableWidth: exportConfig.tableWidth ? exportConfig.tableWidth : 'auto',
      startY: exportConfig.customPdf && exportConfig.customPdf.margin ? exportConfig.customPdf.margin.top : finalY,
      margin: exportConfig.customPdf && exportConfig.customPdf.margin ? exportConfig.customPdf.margin :
        {
          top: this.exportSubtitle!! && this.exportSubtitle.length ? 45 : this.exportTitle!! && this.exportTitle.length ? 35 : 30,
          bottom: 30,
          left: 15,
          right: 15
        },
      didDrawCell: (docdata) => {
        var textPos = docdata.cell.textPos;
        if (exportConfig.rows === null || exportConfig.rows === undefined) {
          var dim = docdata.cell.height - docdata.cell.padding('vertical');
          const customWidth = exportConfig.columnStyles && exportConfig.columnStyles[docdata.column.index]
            && exportConfig.columnStyles[docdata.column.index].contentWidth ? exportConfig.columnStyles[docdata.column.index].contentWidth : dim;
          const columnIdx = exportConfig.checkboxSelect ? docdata.column.index - 1 : docdata.column.index;

          /* Draw checkboxes header */
          if (exportConfig.checkboxSelect && docdata.section === 'head' && docdata.column.index === '0') {
            if (exportConfig.selectedItems.length === exportConfig.data.length) {
              doc.addImage(this.cmacsPdfImages.checkboxSelected, 'PNG', textPos.x, textPos.y, dim, dim, 'checkboxSelected', "FAST");
            } else if (!exportConfig.selectedItems.length) {
              doc.addImage(this.cmacsPdfImages.checkboxEmpty, 'PNG', textPos.x, textPos.y, dim, dim, 'checkboxEmpty', "FAST");
            } else {
              doc.addImage(this.cmacsPdfImages.checkboxIndeterminate, 'PNG', textPos.x, textPos.y, dim, dim, 'checkboxIndeterminate', "FAST");
            }
          }

          /* Draw checkboxes body */
          if (exportConfig.checkboxSelect && docdata.section === 'body' && docdata.column.index === '0') {
            const row = exportConfig.data[docdata.row.index];
            const idx = exportConfig.selectedItems.indexOf(row[exportConfig.config.fieldId]);
            if (idx < 0) {
              doc.addImage(this.cmacsPdfImages.checkboxEmpty, 'PNG', textPos.x, textPos.y, dim, dim, 'checkboxEmpty', "FAST");
            } else {
              doc.addImage(this.cmacsPdfImages.checkboxSelected, 'PNG', textPos.x, textPos.y, dim, dim, 'checkboxSelected', "FAST");
            }
          }

          /* Draw header templates */
          if (exportConfig.config.fields[columnIdx] && exportConfig.config.fields[columnIdx].template && (docdata.section === 'head')) {
            var img = document.getElementById(exportConfig.elemID + 'column' + columnIdx);
            this.setInlineStyles(img);
            this.images.push({ src: img, x: textPos.x, y: textPos.y, width: dim, height: dim });
          }

          /* Draw body templates and tags */
          if (exportConfig.config.fields[columnIdx]
            && (exportConfig.config.fields[columnIdx].celdType === CeldType.TemplateRef
              || exportConfig.config.fields[columnIdx].celdType === CeldType.Tag)
            && (docdata.section === 'body')
          ) {
            var img = document.getElementById(exportConfig.elemID + 'column' + columnIdx + 'row' + docdata.row.index);
            if (img) {
              this.setInlineStyles(img);
              this.images.push({ src: img, x: textPos.x, y: textPos.y, width: customWidth, height: dim });
            }
          }
        }

      /* Draw images in cells */
        if (exportConfig.customCellImages!! && docdata.section === 'body') {
          const customizedImage = exportConfig.customCellImages.find(item => item.row === docdata.row.index &&
            (item.columnOrder === docdata.column.index || item.columnDataKey === docdata.column.dataKey));
          if (customizedImage) {
            doc.addImage(customizedImage.src, 'PNG', textPos.x, textPos.y, customizedImage.width, customizedImage.height, customizedImage.id, "FAST");
          }
        }

        /* Draw borders */
        if ((exportConfig.hideBorders === null || exportConfig.hideBorders === undefined || !exportConfig.hideBorders) && docdata.section === 'body') {
          doc.setDrawColor(222, 224, 229);
          doc.line(docdata.cell.x, docdata.cursor.y, docdata.cell.x + docdata.cell.width, docdata.cursor.y);         
        }

        if ((exportConfig.hideBorders === null || exportConfig.hideBorders === undefined || !exportConfig.hideBorders)) {
          if (exportConfig.customPdf && exportConfig.customPdf.columns) {
            const customColumn = exportConfig.customPdf.columns.find(e => e.marginLeft && e.dataKey === docdata.column.dataKey)
            if (customColumn) {
              doc.setDrawColor(222, 224, 229);
              doc.line(docdata.cursor.x, docdata.cell.y, docdata.cursor.x, docdata.cell.y + docdata.cell.height);
            }             
          }
        }

      },
      willDrawCell: (docdata) => {
        if (exportConfig.customCells!! && docdata.section === 'body') {
          const customizedCell = exportConfig.customCells.filter(item => item.row === docdata.row.index &&
            (item.columnOrder === docdata.column.index || item.columnDataKey === docdata.column.dataKey));
          if (customizedCell.length) {
            this.customizeCell(doc, customizedCell[0]);
          }
        }
      },
      didDrawPage: (data) => {
        // Reseting top margin. The change will be reflected only after print the first page.
        data.settings.margin = { top: this.exportSubtitle!! && this.exportSubtitle.length ? 45 : this.exportTitle!! && this.exportTitle.length ? 35 : 30, bottom: 30, left: 15, right: 15 };
      }
    });
  }

  drawLegendTable(doc, legend) {
    doc.autoTable({
      columns: legend.columns,
      body: legend.rows,
      theme: 'plain',
      showHead: 'never',
      columnStyles: legend.columnStyles,
      bodyStyles: {
        font: 'Roboto',
        minCellHeight: 1,
        minCellWidth: 1,
        fontStyle: 'normal',
        fillColor: '#ffffff',
        textColor: '#97a0ae',
        fontSize: 8
      },
      startY: legend.customPdf && legend.customPdf.margin ? legend.customPdf.margin.top : null,
      margin: legend.customPdf ? legend.customPdf.margin : null,
      willDrawCell: (docdata) => {
        if (docdata.section === 'body') {
          const customizedCell = legend.customCells.find(item => item.row === docdata.row.index &&
            (item.columnOrder === docdata.column.index || item.columnDataKey === docdata.column.dataKey));
          if (customizedCell) {
            this.customizeCell(doc, customizedCell);
          }
        }
      },
    });
  }

  getColumns(exportConfig) {
    const columns = [];
    if (exportConfig.checkboxSelect) {
      columns.push('');
    }
    exportConfig.config.fields.filter(item => item.celdType === CeldType.Default
      || item.celdType === CeldType.Tag
      || item.celdType === CeldType.TemplateRef).forEach(field => {
      columns.push(field.display);
    });
    return columns;
  }

  getRows(exportConfig) {
    const rows = [];
    exportConfig.data.forEach(item => {
      const itemToExport = [];
      if (exportConfig.checkboxSelect) {
        itemToExport.push('');
      }
      // tslint:disable-next-line: no-shadowed-variable
      exportConfig.config.fields.filter(item => item.celdType === CeldType.Default ||
        item.celdType === CeldType.TemplateRef || item.celdType === CeldType.Tag).forEach(field => {
        if (field.celdType === CeldType.TemplateRef || field.celdType === CeldType.Tag) {
          itemToExport.push('');
        } else {
          switch (field.editTemplate) {
            case TemplateType.Select:
              const selectItem = field.select.selectData.find(option => option[field.select.value] === item[field.property]);

              if (selectItem !== undefined) {
                itemToExport.push(selectItem[field.select.label]);
              }
              break;
            case TemplateType.Date:
              itemToExport.push(exportConfig.datePipe.transform(item[field.property], 'MMMM dd yyyy'));
              break;
            case TemplateType.Time:
              itemToExport.push(exportConfig.datePipe.transform(item[field.property], 'h:mm a'));
              break;
            default:
              itemToExport.push(item[field.property]);
              break;
          }
        }
      });
      rows.push(itemToExport);
    });
    return rows;
  }

  renderTemplate(doc, data) {
    doc.setPage(1);
    const img = this.images.pop();
    img.src.style.width = 'fit-content';

    const options = {
      background: null,
      scale: 3,
      allowTaint: true,
      scrollY: -window.pageYOffset,
      scrollX: 0,
      imageTimeout: 0,
      useCORS: true
    };
    html2canvas(img.src, options).then(canvas => {
      const combined = canvas.toDataURL('image/png');
      doc.addImage(combined, 'PNG', img.x, img.y, img.width, img.height, img.src.id, "FAST");
      img.src.style.width = 'inherited';
      if (this.images.length) {
        this.renderTemplate(doc, data);
      } else {
        this.exportToPdfV2(doc);
      }
    });
  }

  exportToPdfV2(doc) {
    const getLogo = () => {
      const canvasL = document.createElement('canvas');
      const ctx = canvasL.getContext('2d');
      canvasL.height = imgLogo.height;
      canvasL.width = imgLogo.width;
      ctx.drawImage(imgLogo, 0, 0);
      const logo = canvasL.toDataURL('image/PNG');
      this.addFooters(doc, logo, imgLogo);
      const filenameFormatted = moment().format("DD.MM.YYYY.HH.mm.ss") + '_' + this.fileName + '.pdf';
      this._exportCompleted.next(true);
      doc.save(filenameFormatted);
    };

    const imgLogo = new Image();
    imgLogo.crossOrigin = "Anonymous";
    imgLogo.onload = getLogo;
    imgLogo.src = this.exportCompanyLogoUrl;
  }

  addCustomImages(doc, images) {
    doc.setPage(1);
    for (let image of images) {
      doc.addImage(image.src, 'PNG', image.x, image.y, image.width, image.height, image.id);
    }
  }

  pxToMm(pixels) {
    const DPR = window.devicePixelRatio;
    const mm = pixels * 2.54 / (96 / DPR) * 10;
    return mm;
  }

  scaleDimension(dim) {
    const maxWidth = 30; 
    const maxHeight = 10;
    let ratio = 0;
    let height = dim.height;
    let width = dim.width;

    if (width > maxWidth) {
      ratio = maxWidth / width;  
      height = height * ratio;
      width = width * ratio; 
    }

    if (height > maxHeight) {
      ratio = maxHeight / height;
      width = width * ratio;
      height = height * ratio;
    }

    return {height, width};
  }

}
