import { Component, EventEmitter, Input, OnInit, Output, ChangeDetectorRef, OnDestroy } from '@angular/core';
import { Subject } from 'rxjs/internal/Subject';
import { NzI18nService } from 'ng-zorro-antd/i18n';
import { takeUntil } from 'rxjs/operators';
// import '../../assets/tinymce/js/tinymce/skins/ui/PlanToBuild_Dark/content.css';
// import '../../assets/tinymce/js/tinymce/skins/content/PlanToBuild_Dark/content.css';
// import '../../assets/tinymce/js/tinymce/skins/ui/PlanToBuild_Dark/content.css';
// import '../../assets/tinymce/js/tinymce/skins/ui/PlanToBuild_Dark/skin.css';
// import '../../assets/tinymce/js/tinymce/skins/ui/PlanToBuild_Dark/content.min.css';

declare var tinymce: any;


@Component({
  selector: 'cmacs-open-editor',
  exportAs: 'cmacsOpenEditor',
  templateUrl: './cmacs-open-editor.component.html',
  styleUrls: ['./cmacs-open-editor.component.css']
})
export class CmacsOpenEditorComponent implements OnDestroy, OnInit {
  showEditor = false;
  @Output() oninit: EventEmitter<any> = new EventEmitter<any>();
  @Output() onchange: EventEmitter<any> = new EventEmitter<any>();
  @Output() onblur: EventEmitter<any> = new EventEmitter<any>();
  @Output() onkeyup: EventEmitter<any> = new EventEmitter<any>();
  @Input() disabled: boolean = false;
  @Input() height = '450px';
  @Input() placeholder = 'Type here ..."';
  @Input() statusbar = false;
  @Input() resize = false;
  @Input() skin = "PlanToBuild_Dark";
  // tslint:disable-next-line: max-line-length
  @Input() toolbarmobile = ['bold italic underline | fontsizeselect', 'forecolor backcolor | alignleft aligncenter alignright alignfull | numlist bullist'];
  @Input() toolbar = ['bold italic underline | fontsizeselect link', 'forecolor backcolor | alignleft aligncenter alignright alignfull | numlist bullist'];
  @Input() tinyMceSettings!: any;
  @Input() id: string = '00000000-0000-0000-0000-000000000000';
  @Input() content: string = '';
  @Output() contentChange: EventEmitter<string> = new EventEmitter<string>();
                        
  private destroy$ = new Subject();

  constructor(private i18n: NzI18nService, private cdr: ChangeDetectorRef) { }

  ngOnInit(): void {

    if (this.tinyMceSettings === undefined) {


      this.tinyMceSettings = {
        id: this.id,
        editor_selector: 'cmacs-editor',
        menubar: false,
        inline: true,
        image_title: true,
        resize: this.resize,
        automatic_uploads: true,
        height: this.height,
        statusbar: this.statusbar,
        file_picker_types: 'image',
        images_upload_url: '#',
        content_css:false,
        // content_css:'PlanToBuild_Dark', //only available in 5.1 or later
        // skin: 'oxide-dark',
        skin: this.skin,
        // skin_url: 'assets/skins/PlanToBuild_Dark',
        placeholder: this.placeholder,
        setup: (editor: any) => {
          editor.on('init', (obj) => {
            this.oninit.emit(obj);
          });
          editor.on('blur', (obj) => {
            this.onblur.emit(obj);
          });
          editor.on('keyup', (obj) => {
            this.onkeyup.emit(obj);
          });
          editor.on('Change', (obj) => {
            this.contentChange.emit(this.content);
            this.onchange.emit(obj);
          });
        },
        plugins: ['image table textcolor link lists '],
        link_default_target: '_blank',
        toolbar: this.toolbar,
        toolbar_persist: false,
        mobile: {
          theme: 'mobile',
          plugins: ['image table textcolor link'],
        skin: "PlanToBuild_Dark",
          toolbar: this.toolbarmobile
        }
      };
    }

    this.i18n.localeChange.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.tinyMceSettings.language = this.i18n.getLocale().locale === 'de' ? 'de' : null;
      this.cdr.detectChanges();
    });

    setTimeout(() => {
      this.cdr.detectChanges();
    }, 100);

  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

}
