import { Directive, Host, Optional } from '@angular/core';
import { CmacsDatePickerComponent } from './date-picker.component';

@Directive({
  selector: 'cmacs-range-picker',
  exportAs: 'cmacsRangePicker'
})
// tslint:disable-next-line:directive-class-suffix
export class CmacsRangePickerComponent {
  constructor(@Optional() @Host() public datePicker: CmacsDatePickerComponent) {
    this.datePicker.isRange = true;
  }
}
