import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  ElementRef,
  HostBinding,
  Input,
  OnDestroy,
  Renderer2,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';
import { BooleanInput, NzDirectionVHType } from 'ng-zorro-antd/core/types';

import { InputBoolean } from 'ng-zorro-antd/core/util';
import { Subscription } from 'rxjs';
import { CmacsListItemExtraComponent } from './list-item-cell';
import { CmacsListComponent } from './list.component';

@Component({
  selector: 'cmacs-list-item, [cmacs-list-item]',
  exportAs: 'cmacsListItem',
  template: `
    <ng-template #actionsTpl>
      <ul cmacs-list-item-actions *ngIf="actions && actions.length > 0" [cmacsActions]="actions"></ul>
      <ng-content select="cmacs-list-item-actions, [cmacs-list-item-actions]"></ng-content>
    </ng-template>
    <ng-template #contentTpl>
      <ng-content select="cmacs-list-item-meta, [cmacs-list-item-meta]"></ng-content>
      <ng-content></ng-content>
      <ng-container *ngIf="content">
        <ng-container *nzStringTemplateOutlet="content">{{ content }}</ng-container>
      </ng-container>
    </ng-template>
    <ng-template #extraTpl>
      <ng-content select="cmacs-list-item-extra, [cmacs-list-item-extra]"></ng-content>
    </ng-template>
    <ng-template #simpleTpl>
      <ng-template [ngTemplateOutlet]="contentTpl"></ng-template>
      <ng-template [ngTemplateOutlet]="extra"></ng-template>
      <ng-template [ngTemplateOutlet]="extraTpl"></ng-template>
      <ng-template [ngTemplateOutlet]="actionsTpl"></ng-template>
    </ng-template>

    <ng-container *ngIf="isVerticalAndExtra; else simpleTpl">
      <div class="ant-list-item-main">
        <ng-template [ngTemplateOutlet]="contentTpl"></ng-template>
        <ng-template [ngTemplateOutlet]="actionsTpl"></ng-template>
      </div>
      <cmacs-list-item-extra *ngIf="extra">
        <ng-template [ngTemplateOutlet]="extra"></ng-template>
      </cmacs-list-item-extra>
      <ng-template [ngTemplateOutlet]="extraTpl"></ng-template>
    </ng-container>
  `,
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CmacsListItemComponent implements OnDestroy, AfterViewInit {
  static ngAcceptInputType_nzNoFlex: BooleanInput;

  @Input() actions: Array<TemplateRef<void>> = [];
  @Input() content?: string | TemplateRef<void>;
  @Input() extra: TemplateRef<void> | null = null;
  @Input() @InputBoolean() @HostBinding('class.ant-list-item-no-flex') nzNoFlex: boolean = false;

  @ContentChild(CmacsListItemExtraComponent) listItemExtraDirective?: CmacsListItemExtraComponent;

  private itemLayout?: NzDirectionVHType;
  private itemLayout$?: Subscription;

  get isVerticalAndExtra(): boolean {
    return this.itemLayout === 'vertical' && (!!this.listItemExtraDirective || !!this.extra);
  }

  constructor(elementRef: ElementRef, renderer: Renderer2, private parentComp: CmacsListComponent, private cdr: ChangeDetectorRef) {
    renderer.addClass(elementRef.nativeElement, 'ant-list-item');
  }

  ngAfterViewInit(): void {
    this.itemLayout$ = this.parentComp.itemLayoutNotify$.subscribe(val => {
      this.itemLayout = val;
      this.cdr.detectChanges();
    });
  }

  ngOnDestroy(): void {
    if (this.itemLayout$) {
      this.itemLayout$.unsubscribe();
    }
  }
}
