import { Direction, Directionality } from '@angular/cdk/bidi';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  SimpleChanges,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';
import { slideAlertMotion } from 'ng-zorro-antd/core/animation';
import { NzConfigKey, NzConfigService, WithConfig } from 'ng-zorro-antd/core/config';
import { BooleanInput } from 'ng-zorro-antd/core/types';
import { InputBoolean } from 'ng-zorro-antd/core/util';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

const NZ_CONFIG_MODULE_NAME: NzConfigKey = 'alert';

@Component({
  selector: 'cmacs-alert',
  exportAs: 'cmacsAlert',
  animations: [slideAlertMotion],
  template: `
    <div
      *ngIf="!closed"
      class="ant-alert"
      [class.ant-alert-rtl]="dir === 'rtl'"
      [class.ant-alert-success]="type === 'success'"
      [class.ant-alert-info]="type === 'info'"
      [class.ant-alert-warning]="type === 'warning'"
      [class.ant-alert-error]="type === 'error'"
      [class.ant-alert-no-icon]="!showIcon"
      [class.ant-alert-banner]="banner"
      [class.ant-alert-closable]="closeable"
      [class.ant-alert-with-description]="!!description"
      [@.disabled]="noAnimation"
      [@slideAlertMotion]
      (@slideAlertMotion.done)="onFadeAnimationDone()"
    >
      <ng-container *ngIf="showIcon">
        <i nz-icon class="ant-alert-icon" [nzType]="iconType || inferredIconType" [nzTheme]="iconTheme"></i>
      </ng-container>
      <div class="ant-alert-content" *ngIf="message || description">
        <span class="ant-alert-message" *ngIf="message">
          <ng-container *nzStringTemplateOutlet="message">{{ message }}</ng-container>
        </span>
        <span class="ant-alert-description" *ngIf="description">
          <ng-container *nzStringTemplateOutlet="description">{{ description }}</ng-container>
        </span>
      </div>
      <button type="button" aria-label="Close" role="button" tabindex="0" *ngIf="closeable || closeText" class="ant-alert-close-icon" (click)="closeAlert()">
        <ng-template #closeDefaultTemplate>
          <i nz-icon nzType="close"></i>
        </ng-template>
        <ng-container *ngIf="closeText; else closeDefaultTemplate">
          <ng-container *nzStringTemplateOutlet="closeText">
            <span class="ant-alert-close-text">{{ closeText }}</span>
          </ng-container>
        </ng-container>
      </button>
    </div>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./cmacs-alert.component.css'],
  preserveWhitespaces: false
})
export class CmacsAlertComponent implements OnChanges, OnDestroy, OnInit {
  readonly _nzModuleName: NzConfigKey = NZ_CONFIG_MODULE_NAME;
  static ngAcceptInputType_closeable: BooleanInput;
  static ngAcceptInputType_showIcon: BooleanInput;
  static ngAcceptInputType_banner: BooleanInput;
  static ngAcceptInputType_noAnimation: BooleanInput;

  @Input() closeText: string | TemplateRef<void> | null = null;
  @Input() iconType: string | null = null;
  @Input() message: string | TemplateRef<void> | null = null;
  @Input() description: string | TemplateRef<void> | null = null;
  @Input() type: 'success' | 'info' | 'warning' | 'error' = 'info';
  @Input() @WithConfig() @InputBoolean() closeable: boolean = false;
  @Input() @WithConfig() @InputBoolean() showIcon: boolean = false;
  @Input() @InputBoolean() banner = false;
  @Input() @InputBoolean() noAnimation = false;
  @Output() readonly onClose = new EventEmitter<boolean>();
  closed = false;
  iconTheme: 'outline' | 'fill' = 'fill';
  inferredIconType: string = 'info-circle';
  dir: Direction = 'ltr';
  private isTypeSet = false;
  private isShowIconSet = false;
  private destroy$ = new Subject();

  constructor(public nzConfigService: NzConfigService, private cdr: ChangeDetectorRef, @Optional() private directionality: Directionality) {
    this.nzConfigService
      .getConfigChangeEventForComponent(NZ_CONFIG_MODULE_NAME)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.cdr.markForCheck();
      });
  }

  ngOnInit(): void {
    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.cdr.detectChanges();
    });

    this.dir = this.directionality.value;
  }

  closeAlert(): void {
    this.closed = true;
  }

  onFadeAnimationDone(): void {
    if (this.closed) {
      this.onClose.emit(true);
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { showIcon, description, type, banner } = changes;
    if (showIcon) {
      this.isShowIconSet = true;
    }
    if (type) {
      this.isTypeSet = true;
      switch (this.type) {
        case 'error':
          this.inferredIconType = 'close-circle';
          break;
        case 'success':
          this.inferredIconType = 'check-circle';
          break;
        case 'info':
          this.inferredIconType = 'info-circle';
          break;
        case 'warning':
          this.inferredIconType = 'exclamation-circle';
          break;
      }
    }
    if (description) {
      this.iconTheme = this.description ? 'outline' : 'fill';
    }
    if (banner) {
      if (!this.isTypeSet) {
        this.type = 'warning';
      }
      if (!this.isShowIconSet) {
        this.showIcon = true;
      }
    }
  }
  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
