import { Direction, Directionality } from '@angular/cdk/bidi';
import {
  AfterContentInit,
  ChangeDetectionStrategy,
  Component,
  ContentChild,
  ElementRef,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  SimpleChanges,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';
import { BooleanInput, NzDirectionVHType, NzSafeAny, NzSizeLDSType } from 'ng-zorro-antd/core/types';
import { InputBoolean } from 'ng-zorro-antd/core/util';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { NzListGrid } from './interface';
import { CmacsListFooterComponent, CmacsListLoadMoreDirective, CmacsListPaginationComponent } from './list-cell';

@Component({
  selector: 'cmacs-list, [cmacs-list]',
  exportAs: 'cmacsList',
  styleUrls: ['./cmacs-list.component.css'],
  template: `
    <ng-template #itemsTpl>
      <div class="ant-list-items">
        <div *ngIf="!classicGrid">
          <ng-container *ngFor="let item of dataSource; let index = index">
            <ng-template [ngTemplateOutlet]="renderItem" [ngTemplateOutletContext]="{ $implicit: item, index: index }"></ng-template>
          </ng-container>       
        </div>
        <div *ngIf="classicGrid">
          <div class="cmacs-classic-grid"
               [style.marginRight.px]="grid.gutter"
               *ngFor="let item of dataSource; let index = index">
            <ng-template [ngTemplateOutlet]="renderItem" [ngTemplateOutletContext]="{ $implicit: item, index: index }"></ng-template>
          </div>
        </div>

        <ng-content></ng-content>
      </div>
    </ng-template>

    <cmacs-list-header *ngIf="header">
      <ng-container *nzStringTemplateOutlet="header">{{ header }}</ng-container>
    </cmacs-list-header>
    <ng-content select="cmacs-list-header"></ng-content>

    <nz-spin [nzSpinning]="loading">
      <ng-container>
        <div *ngIf="loading && dataSource && dataSource.length === 0" [style.min-height.px]="53"></div>
        <div *ngIf="grid && dataSource; else itemsTpl" nz-row [nzGutter]="grid.gutter || null">
          <div
            nz-col
            [nzSpan]="grid.span || null"
            [nzXs]="grid.xs || null"
            [nzSm]="grid.sm || null"
            [nzMd]="grid.md || null"
            [nzLg]="grid.lg || null"
            [nzXl]="grid.xl || null"
            [nzXXl]="grid.xxl || null"
            *ngFor="let item of dataSource; let index = index"
          >
            <ng-template [ngTemplateOutlet]="renderItem" [ngTemplateOutletContext]="{ $implicit: item, index: index }"></ng-template>
          </div>
        </div>
        <cmacs-list-empty *ngIf="!loading && dataSource && dataSource.length === 0" [noResult]="noResult"></cmacs-list-empty>
      </ng-container>
    </nz-spin>

    <cmacs-list-footer *ngIf="footer">
      <ng-container *nzStringTemplateOutlet="footer">{{ footer }}</ng-container>
    </cmacs-list-footer>
    <ng-content select="cmacs-list-footer, [cmacs-list-footer]"></ng-content>

    <ng-template [ngTemplateOutlet]="loadMore"></ng-template>
    <ng-content select="cmacs-list-load-more, [cmacs-list-load-more]"></ng-content>

    <cmacs-list-pagination *ngIf="pagination">
      <ng-template [ngTemplateOutlet]="pagination"></ng-template>
    </cmacs-list-pagination>
    <ng-content select="cmacs-list-pagination, [cmacs-list-pagination]"></ng-content>
  `,
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    '[class.ant-list-rtl]': `dir === 'rtl'`,
    '[class.ant-list-vertical]': 'itemLayout === "vertical"',
    '[class.ant-list-lg]': 'size === "large"',
    '[class.ant-list-sm]': 'size === "small"',
    '[class.ant-list-split]': 'split',
    '[class.ant-list-bordered]': 'bordered',
    '[class.ant-list-loading]': 'loading',
    '[class.ant-list-something-after-last-item]': 'hasSomethingAfterLastItem'
  }
})
export class CmacsListComponent implements AfterContentInit, OnChanges, OnDestroy, OnInit {

  @Input() dataSource?: NzSafeAny[];
  @Input() @InputBoolean() bordered = false;
  @Input() grid?: NzListGrid | '' = '';
  @Input() header?: string | TemplateRef<void>;
  @Input() footer?: string | TemplateRef<void>;
  @Input() itemLayout: NzDirectionVHType = 'horizontal';
  @Input() @InputBoolean() classicGrid = false;
  @Input() renderItem: TemplateRef<void> | null = null;
  @Input() @InputBoolean() loading = false;
  @Input() loadMore: TemplateRef<void> | null = null;
  @Input() pagination?: TemplateRef<void>;
  @Input() size: NzSizeLDSType = 'default';
  @Input() @InputBoolean() split = true;
  @Input() noResult?: string | TemplateRef<void>;

  @ContentChild(CmacsListFooterComponent) nzListFooterComponent!: CmacsListFooterComponent;
  @ContentChild(CmacsListPaginationComponent) nzListPaginationComponent!: CmacsListPaginationComponent;
  @ContentChild(CmacsListLoadMoreDirective) nzListLoadMoreDirective!: CmacsListLoadMoreDirective;

  hasSomethingAfterLastItem = false;
  dir: Direction = 'ltr';
  private itemLayoutNotifySource = new BehaviorSubject<NzDirectionVHType>(this.itemLayout);
  private destroy$ = new Subject<void>();

  get itemLayoutNotify$(): Observable<NzDirectionVHType> {
    return this.itemLayoutNotifySource.asObservable();
  }

  constructor(private elementRef: ElementRef, @Optional() private directionality: Directionality) {
    // TODO: move to host after View Engine deprecation
    this.elementRef.nativeElement.classList.add('ant-list');
  }
  ngOnInit(): void {
    this.dir = this.directionality.value;
    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
    });
  }

  getSomethingAfterLastItem(): boolean {
    return !!(
      this.loadMore ||
      this.pagination ||
      this.footer ||
      this.nzListFooterComponent ||
      this.nzListPaginationComponent ||
      this.nzListLoadMoreDirective
    );
  }
  ngOnChanges(changes: SimpleChanges): void {
    if (changes.itemLayout) {
      this.itemLayoutNotifySource.next(this.itemLayout);
    }
  }

  ngOnDestroy(): void {
    this.itemLayoutNotifySource.unsubscribe();
    this.destroy$.next();
    this.destroy$.complete();
  }

  ngAfterContentInit(): void {
    this.hasSomethingAfterLastItem = this.getSomethingAfterLastItem();
  }
}
