import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  Input,
  OnChanges,
  SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';

import { TimelineService } from './timeline.service';
import { NzTimelineItemColor, NzTimelinePosition, TimelineTimeDefaultColors } from './typings';

function isDefaultColor(color?: string): boolean {
  return TimelineTimeDefaultColors.findIndex(i => i === color) !== -1;
}

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  selector: 'cmacs-timeline-item, [cmacs-timeline-item]',
  exportAs: 'cmacsTimelineItem',
  styleUrls: ['./cmacs-timeline-item.component.css'],
  template: `
    <ng-template #template>
      <li
        class="ant-timeline-item"
        [class.ant-timeline-item-right]="(cmacsPosition || position) === 'right'"
        [class.ant-timeline-item-left]="(cmacsPosition || position) === 'left'"
        [class.ant-timeline-item-last]="isLast"
      >
        <div class="ant-timeline-item-tail"></div>
        <div
          class="ant-timeline-item-head"
          [class.ant-timeline-item-head-red]="color === 'red'"
          [class.ant-timeline-item-head-blue]="color === 'blue'"
          [class.ant-timeline-item-head-green]="color === 'green'"
          [class.ant-timeline-item-head-gray]="color === 'gray'"
          [class.ant-timeline-item-head-custom]="!!dot"
          [style.border-color]="borderColor"
        >
          <ng-container *nzStringTemplateOutlet="dot">{{ dot }}</ng-container>
        </div>
        <div class="ant-timeline-item-content">
          <ng-content></ng-content>
        </div>
      </li>
    </ng-template>
  `
})
export class CmacsTimelineItemComponent implements OnChanges {
  @ViewChild('template', { static: false }) template!: TemplateRef<void>;

  @Input() cmacsPosition?: NzTimelinePosition;
  @Input() color: NzTimelineItemColor = 'gray';
  @Input() dot?: string | TemplateRef<void>;

  isLast = false;
  borderColor: string | null = null;
  position?: NzTimelinePosition;

  constructor(private cdr: ChangeDetectorRef, private timelineService: TimelineService) {}

  ngOnChanges(changes: SimpleChanges): void {
    this.timelineService.markForCheck();
    if (changes.color) {
      this.updateCustomColor();
    }
  }

  detectChanges(): void {
    this.cdr.detectChanges();
  }

  private updateCustomColor(): void {
    this.borderColor = isDefaultColor(this.color) ? null : this.color;
  }
}
