import { Component, OnInit, ViewChild, TemplateRef, Output, EventEmitter, Input, AfterViewInit, ElementRef, OnChanges, Renderer2, ChangeDetectorRef } from '@angular/core';
import { WidgetActionType } from '../core/enums/widget-action-type.enum';
import { UtilService } from '../core/services/util.service';

@Component({
  selector: 'cmacs-kpi-group-total',
  templateUrl: './cmacs-kpi-group-total.component.html',
  styleUrls: ['./cmacs-kpi-group-total.component.css']
})
export class CmacsKpiGroupTotalComponent implements OnInit, AfterViewInit, OnChanges {

  @ViewChild('columnTemplate', { read: TemplateRef, static: false }) columnTemplate: TemplateRef<{}>;
  @ViewChild('chartcanvas', { read: ElementRef, static: false }) canvasRef: ElementRef;
  @ViewChild('legendContent', { read: ElementRef, static: false }) public legendContent: ElementRef<any>;

  @Output() clickMenu = new EventEmitter<any>();

  // container
  @Input() headerText = '';
  @Input() footerText = '';
  @Input() footerValue = '';

  // content
  // width, height
  @Input() view!: number[];
  @Input() data!: any[];
  @Input() columnsHeader: string[] = [];

  displayArrowBtns = true;

  // chart
  chartWidth = 104;
  fontChartNumber = 20;
  col2 = 150;
  col3 = 70;
  minWidth = 300;
  showChart = false;
  scrollY = 200;
  p = 1;
  scroll = {x: '300px', y: this.scrollY + 'px' };

  dataTable: any[];
  configurationExpandableRows: any;
  loading = true;
  selectedItem!: string;

  constructor(
    //
    private renderer: Renderer2,
    private elementRef: ElementRef,
    private utilService: UtilService
    //
  ) { }

  ngOnInit(): void {
  }

  ngOnChanges(): void {
    this.setScroll();
    //
    this.p = 1;
    if (this.view && this.view.length === 2) {
      this.p = this.view[0] > this.minWidth ? this.view[0] / this.minWidth : 1;
      if (this.renderer) {
        this.resize();
      }
    }
    //
    this.showChart = false;
    setTimeout(() => {
      if (this.data.length > 0) {
        this.selectedItem = this.data[0].key;
      }
      this.drawCanvas();
      this.setConfiguration();
      this.setData();
    }, 0);
    this.showChart = true;

    this.showArrowBtns();
  }

  ngAfterViewInit(): void {
    this.resize();
    this.showArrowBtns();
  }

  resize(): void {
    const width = this.view[0];
    this.renderer.setStyle(this.elementRef.nativeElement, 'width', `${width}px`);
  }
  //

  setScroll(): void {
    let p = 1;
    if (this.view && this.view.length === 2) {
      p = this.view[1] * 0.5 > this.scrollY ? this.view[1] * 0.5 / this.scrollY : 1;
    }
    this.scroll = {x: '300px', y: this.scrollY * p + 'px' };
  }

  scrollRight(): void {
    this.legendContent.nativeElement.scrollTo({ left: (this.legendContent.nativeElement.scrollLeft - 40), behavior: 'smooth' });
  }

  scrollLeft(): void {
    this.legendContent.nativeElement.scrollTo({ left: (this.legendContent.nativeElement.scrollLeft + 40), behavior: 'smooth' });
  }

  drawCanvas(): void {
    const item = this.data.find(x => x.key === this.selectedItem);
    if (this.canvasRef && item) {
      const canvas = this.canvasRef.nativeElement as HTMLCanvasElement;

      canvas.width = this.chartWidth * this.p;
      canvas.height = this.chartWidth * this.p;
      //
      const ctx = canvas.getContext('2d');
      ctx.clearRect(0, 0, canvas.width, canvas.height);

      // totals
      const totalShow = this.getTotalCateg(item.data);
      const total = this.data.map(x => this.getTotalCateg(x.data)).reduce((a, b) => a + b, 0);
      let startAngle = 0;

      // draw data
      for (const categ of this.data) {
        const totalG =  this.getTotalCateg(categ.data);
        const sliceAngle = 2 * Math.PI * totalG / total;
        if (sliceAngle > 0 ) {
          this.drawPieSlice(
            ctx,
            canvas.width / 2,
            canvas.height / 2,
            Math.min(canvas.width / 2, canvas.height / 2) - ((this.selectedItem === categ.key) ? 10 * this.p : 6 * this.p),
            startAngle,
            startAngle + sliceAngle - 0.05,
            categ.color,
            (this.selectedItem === categ.key) ? 15 * this.p : 7 * this.p
          );
          startAngle += sliceAngle;
        }
      }
      // draw value
      ctx.font = this.fontChartNumber * this.p + 'px Roboto-Regular ';
      ctx.fillStyle = '#3b3f46';
      ctx.textAlign = 'center';
      ctx.fillText('' + totalShow, canvas.width / 2, canvas.width / 2 + 5);

      ///
      canvas.onclick = (e) => {
        /// adjust mouse position to be relative to canvas
        const rect = canvas.getBoundingClientRect();
        const x = e.clientX - rect.left;
        const y = e.clientY - rect.top;

        /// grab a pixel
        const data = ctx.getImageData(x, y, 1, 1).data;
        const rgbToHex = '#' + [data[0], data[1], data[2]].map(w => w.toString(16).padStart(2, '0')).join('');

        const itemSelected = this.data.find(z => z.color.toLowerCase() === rgbToHex);
        if (itemSelected) {
          this.expandTable(itemSelected.key);
        }
      };

    }
  }

  drawPieSlice(ctx: CanvasRenderingContext2D, centerX: number, centerY: number,
               radius: number, startAngle: number, endAngle: number, color: any, lineWidth: number ): void {
    ctx.beginPath();
    ctx.arc(centerX, centerY, radius, startAngle - Math.PI / 2, endAngle - Math.PI / 2, false);
    ctx.lineWidth = lineWidth;
    ctx.strokeStyle = color;
    ctx.stroke();
  }

  getTotalCateg(data: any[]): number {
    return data.map(x => x.value).reduce((a, b) => a + b, 0);
  }

  setConfiguration(): void {
    //
    this.configurationExpandableRows = {
      fields: [
        {
          celdType: 3,
          display: '',
          property: 'color',
          width: '60px',
          minWidth: 60,
          maxWidth: 120,
          resizable: true
        },
        {
          celdType: 0,
          display: this.columnsHeader[0],
          property: 'name',
          editTemplate: 3,
          width: this.col2 * this.p + 'px',
          minWidth: this.col2 * this.p - 10,
          maxWidth: this.col2 * this.p + 100,
          resizable: true
        },
        {
          celdType: 0,
          display: this.columnsHeader[1],
          property: 'value',
          width: this.col3 * this.p + 'px',
          minWidth: this.col3 * this.p - 10,
          editTemplate: 2,
          editable: false
        }
      ],
      fieldId: 'key',
      fieldRate: 'fav'
    };
  }

  setData(): void {
    this.loading = true;
    const temp: any[] = [];
    for (const data of this.data) {
      for (const item of data.data) {
        const isExpanded = (data.key === this.selectedItem);
        const newItem = {
          key: item.key,
          name: item.name,
          color:  {
            ref: this.columnTemplate,
            context: {
              color: data.color
            }
          },
          expand: isExpanded,
          value: item.value,
          children: this.getChildren(item.rows)
        };
        (data.key === this.selectedItem) ? temp.unshift(newItem) : temp.push(newItem);
      }
    }
    this.dataTable = temp;
    setTimeout(() => {
      this.loading = false;
    }, 0);
  }
  getChildren(items: any[]): any[] {
    const output: any[] = [];
    for (const item of items) {
      output.push({
        key: item.key,
        name: item.name,
        color: {},
        value: ''
      });
    }
    return output;
  }

  menuClick(type: WidgetActionType): void {
    this.clickMenu.emit(type);
  }

  expandTable(key: string): void {
    this.selectedItem = key;
    setTimeout(() => {
       this.setData();
    }, 0);
    this.drawCanvas();
  }

  showArrowBtns(): void {
    const text = this.data.map(i => i.name + 'space');
    const tsize = this.utilService.get_tex_size(text, '12px Roboto-Regular');
    this.displayArrowBtns = tsize.width > this.view[0] - 15;
  }

  public getDataTable(): any[] {
    return this.dataTable;
  }

  public getConfiguration(): any {
    return this.configurationExpandableRows;
  }

  public getChartImage(): any {
    return this.canvasRef.nativeElement.toDataURL('image/png');
  }

}
