import { DatePipe } from '@angular/common';
import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
  AfterViewInit,
  TemplateRef,
  ViewChild,
  HostListener,
  ChangeDetectionStrategy
} from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil, count, delay, repeat } from 'rxjs/operators';
import { NzI18nService } from 'ng-zorro-antd/i18n';
import { ExportAsService } from 'ngx-export-as';
import { GridConfig, Field } from '../core/interfaces/grid-config';
import { GridExpConfig } from '../core/interfaces/grid-exp-config';
import { TemplateType } from '../core/enums/TemplateType.enum';
import { CeldType } from '../core/enums/CeldType.enum';
import { ExcelService } from '../core/services/excel.service';
import { ExportType } from '../core/enums/export-type.enum';
import { CookieService } from 'ngx-cookie-service';
import * as moment_ from 'moment';
import 'moment/locale/en-ie';
import { UtilService } from '../core/services/util.service';
import { NzSizeMDSType } from 'ng-zorro-antd/core/types';
import { InputBoolean, InputNumber, toBoolean } from 'ng-zorro-antd/core/util';
import { CmacsContextMenuService } from '../cmacs-dropdown/context-menu.service';
import { CmacsInputNumberComponent } from '../cmacs-input-number/cmacs-input-number.component';
import { NzResizeEvent } from 'ng-zorro-antd/resizable';

const moment = moment_;

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'cmacs-table',
  exportAs: 'cmacsTable',
  templateUrl: './cmacs-table.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styleUrls: ['./cmacs-table.component.css']
})
// tslint:disable-next-line no-any
export class CmacsTableComponent implements OnInit, OnChanges, OnDestroy, AfterViewInit {

  constructor(
    private cdr: ChangeDetectorRef,
    private i18n: NzI18nService,
    private exportAsService: ExportAsService,
    private cmacsContextMenuService: CmacsContextMenuService,
    private excelService: ExcelService,
    private utilService: UtilService,
    private datePipe: DatePipe,
    private cookies: CookieService
  ) {
    // renderer.addClass(elementRef.nativeElement, 'ant-table-wrapper');
  }
  locale: any = {}; // tslint:disable-line:no-any
  headerBottomStyle = {};
  private destroy$ = new Subject<void>();
  // tslint:disable-next-line: no-input-rename
  @Input() size: NzSizeMDSType = 'default';
  @Input() showTotal: TemplateRef<{ $implicit: number; range: [number, number] }>;
  @Input() pageSizeOptions = [10, 20, 30, 40, 50];
  @Input() @InputBoolean() virtualScroll = false;
  @Input() @InputNumber() virtualItemSize = 0;
  @Input() loadingDelay = 0;
  @Input() loadingIndicator: TemplateRef<void>;
  @Input() total = 0;
  @Input() title: string | TemplateRef<void>;
  @Input() footer: string | TemplateRef<void>;
  @Input() noResult: string | TemplateRef<void>;
  @Input() widthConfig: string[] = [];
  @Input() pageIndex = 1;
  @Input() pageSize = 10;
  // tslint:disable-next-line: no-input-rename
  @Input() data = [];
  @Input() config: GridConfig;
  @Output() configChange: EventEmitter<GridConfig> = new EventEmitter<GridConfig>();
  @Input() fieldId: string;
  @Input() gridID: string;
  @Input() paginationPosition: 'top' | 'bottom' | 'both' = 'bottom';
  @Input() scroll: { x?: string | null; y?: string | null } = { x: null, y: null };
  @ViewChild('renderItemTemplate') nzItemRender: TemplateRef<{
    $implicit: 'page' | 'prev' | 'next';
    page: number;
  }>;
  @Input() @InputBoolean() printLandscape = true;
  @Input() @InputBoolean() wrapLines = false;
  @Input() @InputBoolean() frontPagination = true;
  @Input() @InputBoolean() templateMode = false;
  @Input() @InputBoolean() bordered = false;
  @Input() @InputBoolean() showPagination = true;
  @Input() @InputBoolean() loading = false;
  @Input() @InputBoolean() showSizeChanger = false;
  @Input() @InputBoolean() hideOnSinglePage = false;
  @Input() @InputBoolean() showQuickJumper = false;
  @Input() @InputBoolean() simple = false;
  @Input() @InputBoolean() checkboxSelect = false;
  @Input() @InputBoolean() inLineEdit = false;
  @Input() @InputBoolean() dataTable = false;
  @Input() @InputBoolean() showRate = false;
  @Input() @InputBoolean() hasCookies = true;
  @Input() exportEvent = new EventEmitter<GridExpConfig>();
  @Output() buttonClick = new EventEmitter<any>();
  @Output() rateChange = new EventEmitter<any>();
  @Output() selectionChange = new EventEmitter<any[]>();
  @Output() ondlclickRow = new EventEmitter<any>();
  @Output() onclickRow = new EventEmitter<any>();
  @Output() onclickFavorite = new EventEmitter<any>();
  @Output() onedit = new EventEmitter<any>();
  @Input() rateCount = 5;
  @Input() @InputBoolean() multiSelect = false;
  @Output() sortChange = new EventEmitter<any>();
  @Output() onresize = new EventEmitter<any>();

  @Input() contextmenu: string | TemplateRef<void>;
  public dropdown: any;

  selected = false;
  defaultSortOrder = null;
  checkboxCache: CheckboxSelect[] = [];
  isIndeterminate = false;
  allChecked = false;
  editId: string | null;
  property: string | null;
  rowOnEdition = -1;
  tapCount = 0;
  lastIdxSelected = null;

  @ViewChild('fieldTypeInput', { read: ElementRef }) inputElement: ElementRef;
  @ViewChild('fieldTypeInputNumber', { read: ElementRef }) inputNumberElement: ElementRef;
  @ViewChild(CmacsInputNumberComponent, { read: CmacsInputNumberComponent }) inputNumberComponent: CmacsInputNumberComponent;
  @ViewChild('fieldTypeDatePicker', { read: ElementRef }) datePickerElement: ElementRef;
  @ViewChild('fieldTypeSelect', { read: ElementRef }) selectElement: ElementRef;
  @ViewChild('fieldTypeBool', { read: ElementRef }) boolElement: ElementRef;

  private _onresize$ = new Subject<any>();
  public onresizeobs = this._onresize$.asObservable();

  private _onsort$ = new Subject<any>();
  public onsortobs = this._onsort$.asObservable();

  order = 0;

  clicks = 0;

  contextMenu($event: MouseEvent, template: any): void {
    this.cmacsContextMenuService.create($event, template);
  }

  startEdit(id: string, property: string, event: MouseEvent): void {
    event.preventDefault();
    event.stopPropagation();
    this.editId = id;
    this.property = property;
    this.cdr.detectChanges();
    this.focusSelect(this.inputElement);
    if (this.inputNumberComponent) {
      this.focusSelect(this.inputNumberComponent.inputElement);
    }
  }

  sort($event: any, fieldProperty: string,): void {
    this.cdr.detectChanges();
    this._onsort$.next({ sortName: fieldProperty, sortValue: $event });
  }

  onResize(event: NzResizeEvent, col: string, field: Field): void {

    this._onresize$.next({ event, col, field });
  }

  onResizeEvt({ width }: NzResizeEvent, col: string, field: Field): void {

    if (this.config && this.config.fields) {
      for (let i = 0; i < this.config.fields.length; i++) {
        const field = this.config.fields[i];
        if (field.property === col) {
          const elWidth = field.width ? Number(field.width.replace(/[^\d.-]/g, '')) : 0;
          if (this.scroll.x && i + 1 === this.config.fields.length - 1 &&
            this.config.fields[i + 1] !== null && this.config.fields[i + 1] !== undefined) {
            const elWidthNext = this.config.fields[i + 1].width ? Number(this.config.fields[i + 1].width.replace(/[^\d.-]/g, '')) : 0;
            const delta = width - elWidth;
            if (delta > 0 && delta < elWidthNext) {
              if (this.config.fields[i + 1].minWidth !== null && this.config.fields[i + 1].minWidth !== undefined) {
                if (Number(this.config.fields[i + 1].minWidth) < (elWidthNext - delta)) {
                  this.config.fields[i + 1].width = `${elWidthNext - delta}px`;
                }
              } else {
                this.config.fields[i + 1].width = `${elWidthNext - delta}px`;
              }
            }
            if (delta < 0) {
              this.config.fields[i + 1].width = `${elWidthNext - delta}px`;
            }
          }
          field.width = `${width}px`;
          // this.scroll.x = this.scroll.x ? `${Number(this.scroll.x.replace(/[^\d.-]/g, '')) - elWidth + width}px` : null;
          continue;
        }
      }
    }
    this.onresize.emit({ col, config: this.config });
    this.configChange.emit(this.config);
    if (this.gridID === null || this.gridID === undefined) {
      return;
    }
    if (this.hasCookies && this.getIndexCookie()) {
      this.cookies.set(this.gridID, JSON.stringify(this.config), 365);
    }
  }

  @HostListener('document:mousedown', ['$event'])
  handleMouseDown(e: Event): void {
    const element = e.target as HTMLElement;
    if (this.editId && this.property) {
      if (
        (this.inputElement && this.inputElement.nativeElement !== e.target) ||
        (this.inputNumberElement && !this.childOf(element, this.inputNumberElement.nativeElement)) ||
        (this.datePickerElement && !this.childOf(element, this.datePickerElement.nativeElement)) ||
        (this.selectElement && !this.childOf(element, this.selectElement.nativeElement) ||
          (this.boolElement && !this.childOf(element, this.boolElement.nativeElement)))
      ) {
        if (this.inputNumberComponent) {
          this.inputNumberComponent.blur();
        }
        this.editId = null;
        this.property = null;
        if (this.rowOnEdition >= 0) {
          this.onedit.emit(this.data[this.rowOnEdition]);
          this.rowOnEdition = -1;
        }
      }
    }
  }

  focusSelect(elem: any): void {
    if (elem) {
      elem.nativeElement.focus();
      setTimeout(() => {
        elem.nativeElement.select();
      }, 100);
    }
  }

  getHeaderMaxWidth(field: Field): string {
    if (field.showSort) {
      return `calc(100% - 15px)`;
    }
    return `100%`;
  }

  childOf(node: any, ancestor: any): boolean {
    let child = node;
    while (child !== null) {
      if (child === ancestor) { return true; }
      if (child.classList && child.classList.length > 0 && child.className && typeof child.className === 'string'
        && child.className.indexOf('cdk-overlay-pane') >= 0) {
        for (let i = 0; i < child.childNodes.length; ++i) {
          const node = child.childNodes[i];
          if (node.nodeName === 'CMACS-MODAL') { return false; }
        }
        return true;
      }
      child = child.parentNode;
    }
    return false;
  }

  endEditMode($event: KeyboardEvent, index: number): void {
    if ($event.key === ('Enter' || 'enter')) {
      this.editId = null;
      this.property = null;
      this.rowOnEdition = -1;
      this.onedit.emit(this.data[index]);
    } else {
      this.rowOnEdition = index;
    }
  }

  endEditModeNgModel(index: number): void {
    this.editId = null;
    this.property = null;
    this.rowOnEdition = -1;
    this.onedit.emit(this.data[index]);
  }

  getIndex(id): number {
    let i = -1;
    i = this.checkboxCache.findIndex(item => item.data[this.config.fieldId] === id);
    return i;
  }
  updateCheckboxCache(): void {
    this.checkboxCache.length = 0;
    this.data.forEach(item => {
      this.checkboxCache.push({
        selected: item.selected ? item.selected : false,
        data: { ...item },
        order: item.selected ? this.order++ : -1
      });
    });
  }

  getMaxWidth(): string {
    if (!this.inLineEdit) {
      return `100%`;
    }
    return `calc(100% - 20px)`;
  }

  onButtonClick(field: any): void {
    this.buttonClick.emit(field);
  }

  onCheckboxChange($event, data): void {
    this.checkboxCache[this.getIndex(data[this.config.fieldId])].order = $event ? this.order++ : -1;
    this.refreshCheckboxState();
  }

  refreshCheckboxState(oninit = false): void {
    const dataSelected = this.checkCheckboxState();
    if (!oninit) {
      this.selectionChange.emit(dataSelected.map(item => item.data));
    }
  }

  checkCheckboxState(): CheckboxSelect[] {
    const dataSelected = this.checkboxCache.filter(item => item.selected).sort((a, b) => a.order - b.order);
    this.selected = this.allChecked = (dataSelected.length > 0 && (dataSelected.length === this.checkboxCache.length));
    this.isIndeterminate = dataSelected.length > 0 && !this.allChecked;
    return dataSelected;
  }

  ngModelChange(index: number): void {
    this.rowOnEdition = index;
  }

  // tslint:disable-next-line: no-shadowed-variable
  onRateChange(count: number, data: any): void {
    data[this.config.fieldRate] = count;
    this.rateChange.emit(data);
  }

  onFavoriteChange(isFavorite: boolean, field: Field, data: any): void {
    data[field.property] = isFavorite;
    this.onclickFavorite.emit(data);
  }

  onRateClick(event: any): void {
    this.preventDefault(event);
  }

  getFields(): Field[] {
    return this.config.fields.filter(item => item.hidden === undefined || !item.hidden);
  }

  onCheckboxAllChange(status: boolean): void {
    this.checkboxCache.forEach(element => {
      element.selected = status;
    });
    this.isIndeterminate = false;

    this.selectionChange.emit((status) ? this.data : []);
  }

  getLabel(data: any, field: Field): string {
    let result = '';
    if (field.editTemplate === TemplateType.Select && field.select !== undefined) {
      // tslint:disable-next-line: no-shadowed-variable
      const item = field.select.selectData.find(item => item !== undefined && item[field.select.value] === data[field.property]);
      result = (item !== undefined) ? item[field.select.label] : '';
    }

    return result;
  }

  isSelect(field: Field): boolean {
    return field !== undefined && field.editTemplate === TemplateType.Select;
  }

  isReadOnly(field: Field): boolean {
    return field !== undefined && field.readonly !== undefined && field.readonly;
  }

  isBoolean(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.Default && field.editTemplate === TemplateType.Boolean;
  }

  isObject(value: any): boolean {
    return value !== undefined && typeof (value) === 'object';
  }

  isDate(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.Default && field.editTemplate === TemplateType.Date;
  }

  isString(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.Default && field.editTemplate === TemplateType.String;
  }

  isNumber(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.Default && field.editTemplate === TemplateType.Number;
  }

  isCeldTypeDefault(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.Default;
  }

  isCeldTypeButton(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.Button;
  }

  isCeldTypeTag(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.Tag;
  }

  isCeldTypeTemplateRef(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.TemplateRef;
  }

  isCeldTypeButtonFavorite(field: Field): boolean {
    return field !== undefined && field.celdType === CeldType.Favorite;
  }

  isUndefined(value: any): boolean {
    return value === undefined;
  }

  isRowSelected(data: any): boolean {
    const dataSelectd = this.checkboxCache.filter(item => item.selected).map(item => item.data[this.config.fieldId]);
    return dataSelectd.indexOf(data[this.config.fieldId]) !== -1;
  }

  ngAfterViewInit(): void {
    if (this.hasCookies && this.getIndexCookie() && this.cookies.check(this.gridID)) {
      const savedConfigStr = this.cookies.get(this.gridID);
      // reset expiration date
      this.cookies.set(this.gridID, savedConfigStr, 365);

      // parse cookie value to typescript const
      const savedConfig = JSON.parse(this.cookies.get(this.gridID)) as GridConfig;
      if (typeof savedConfig === 'object') {
        this.config = savedConfig;
        this.configChange.emit(this.config);
      }
    }
    this.cdr.detectChanges();
  }

  getIndexCookie(): boolean {
    return true;
    /*let allowIndexPageCookie = false;
    let consentCookie = this.cookies.get('OptanonConsent');
    if (consentCookie != "") {
      let groupIndex = consentCookie.indexOf('groups=');
      let groups = consentCookie.substring(groupIndex); //will return somethinglike groups=C0002:0,C0001:1
      let functionalGroupIndex = groups.indexOf('C0009:');
      if (functionalGroupIndex != -1) {
        const categoryValue = groups.substring(functionalGroupIndex + 6, functionalGroupIndex + 7);
        if (Number(categoryValue) === 1) {
          allowIndexPageCookie = true;
        }
      }
    }
    return allowIndexPageCookie;*/
  }

  ngOnInit(): void {
    this.i18n.localeChange.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.locale = this.i18n.getLocaleData('Table');
      this.cdr.markForCheck();
    });

    if (this.checkboxSelect) {
      this.updateCheckboxCache();
      this.refreshCheckboxState(true);
    }

    if (this.dataTable && this.data.length > 1) {
      while (this.data.length > 1) {
        this.data.pop();
      }
      this.showPagination = false;
    }


    this.checkCheckboxState();

    if (!this.gridID) {
      this.gridID = this.utilService.uuidv4();
    }

    this.exportEvent.subscribe((config: GridExpConfig) => {

      switch (config.exportType) {
        case ExportType.Pdf:
          this.exportToPdf(config);
          break;
        case ExportType.Xslx:
          this.exportToExcel(config.fileName);
          break;
        case ExportType.Png:
          this.exportToPng(config.fileName);
          break;
      }
    });

    this.utilService.exportCompleted.subscribe(() => {
      this.loading = false;
      this.cdr.markForCheck();
    });

    this.excelService.exportCompleted.subscribe(() => {
      this.loading = false;
      this.cdr.markForCheck();
    });
    this.checkSortEvent();
    this.checkResizeEvent();
  }


  checkSortEvent(): void {
    this.onsortobs.pipe(
      takeUntil(this._onresize$),
      delay(300),
      repeat(),
      takeUntil(this.destroy$)
    ).subscribe((obj: any) => {
      const f = this.config.fields.find(el => el.property === obj.sortName);
      f.sortOrder = obj.sortValue;
      this.sortChange.emit({ sortName: obj.sortName, sortValue: obj.sortValue });
    });
  }

  checkResizeEvent(): void {
    this.onresizeobs.pipe(
      takeUntil(this.destroy$)
    ).subscribe((obj: { event: NzResizeEvent, col: string, field: Field }) => {
      this.config = JSON.parse(JSON.stringify(this.config));
      this.onResizeEvt(obj.event, obj.col, obj.field);
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.data) {
      this.updateCheckboxCache();
      this.refreshCheckboxState();
    }
  }

  exportToPng(fileName: string): void {
    this.loading = true;
    this.utilService.exportTable('png', fileName, this.gridID);
  }

  exportToExcel(fileName: string): void {
    this.loading = true;
    const dataToExport: any[] = [];
    const fields = this.config.fields.filter(item =>
      item.celdType === CeldType.Default ||
      item.celdType === CeldType.Tag ||
      item.celdType === CeldType.TemplateRef);

    this.data.forEach(item => {
      const itemToExport = {};
      // tslint:disable-next-line: no-shadowed-variable
      fields.forEach(field => {
        if (!field.hidden) {
          if (field.editTemplate === TemplateType.Select) {
            const selectItem = field.select.selectData.find(option => option[field.select.value] === item[field.property]);
            if (selectItem !== undefined) {
              itemToExport[field.display] = selectItem[field.select.label];
            }
          } else if (field.celdType === CeldType.TemplateRef) {
            itemToExport[field.display] = item[field.property].context.exportValue;
          } else {
            itemToExport[field.display] = item[field.property];
          }
        }
      });

      dataToExport.push(itemToExport);
    });

    const filenameFormatted = moment().format('DD.MM.YYYY.HH.mm.ss') + '_' + fileName;
    this.excelService.exportAsExcelFile(dataToExport, filenameFormatted);
  }

  exportToPdf(config): void {
    this.loading = true;
    this.cdr.detectChanges();
    if (!config.useVersion2) {
      this.utilService.exportTable(
        'pdf',
        config.fileName,
        this.gridID,
        config.exportCompanyName,
        config.exportUserName,
        config.exportTitle,
        config.exportCompanyLogoUrl,
        config.exportTableCustomWidth,
        config.exportTableCustomHeight
      );
    } else {
      const exportConfig = {
        checkboxSelect: this.checkboxSelect,
        selectedItems: this.checkboxSelect ?
          this.checkboxCache.filter(item => item.selected).map(item => item.data[this.config.fieldId]) : [],
        fileName: config.fileName,
        datePipe: this.datePipe,
        elemID: this.gridID,
        data: this.data,
        config: this.config,
        columnStyles: config.columnStyles,
        exportCompanyName: config.exportCompanyName,
        exportUserName: config.exportUserName,
        exportTitle: config.exportTitle,
        exportCompanyLogoUrl: config.exportCompanyLogoUrl ? config.exportCompanyLogoUrl : 'assets/PToB_logo.png',
      };

      this.utilService.exportTablev2(
        exportConfig
      );
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  tapHandler($event, data): void {
    this.tapCount += 1;
    setTimeout(() => {
      if (this.tapCount === 1) {
        $event.preventDefault();
        this.clickRow(null, data);
      }
      this.tapCount = 0;
    }, 600);
    if (this.tapCount > 1) {
      $event.preventDefault();
      this.dblClickRow(data);
    }

  }
  clickRow(event: MouseEvent, data: any): void {
    this.clicks++;
    setTimeout(() => {
      event.preventDefault();
      event.stopImmediatePropagation();
      if (this.clicks === 1) {
        this.selectRow(event, data);
      }
      if (this.clicks > 1) {
        this.dblClickRow(data);
      }
      this.clicks = 0;
    }, 300);
  }

  preventDefault($event: MouseEvent): void {
    $event.preventDefault();
    $event.stopImmediatePropagation();
  }

  selectRow(event: MouseEvent, data: any): void {
    this.onclickRow.emit(data);
    /* Get index of selection */
    const index = this.checkboxCache.findIndex(item => item.data[this.config.fieldId] === data[this.config.fieldId]);
    if (!this.checkboxSelect) {
      if (event && (toBoolean(event.ctrlKey) || toBoolean(event.shiftKey))) {
        /* Shift Selection */
        if (toBoolean(event.shiftKey)) {
          this.selectMultiple(index);
        }
      } else {
        this.removeSelection(data);
      }
      /* Select element */
      if (index !== -1) {
        this.checkboxCache[index].selected = event && toBoolean(event.shiftKey) ? true : !this.checkboxCache[index].selected;
        /* Save last index selected */
        this.lastIdxSelected = this.checkboxCache[index].selected ? index : null;
      }
      const selectedItems = this.checkboxCache.filter(item => item.selected);
      this.checkboxCache[index].order = this.checkboxCache[index].selected ? this.order++ : -1;
      this.selectionChange.emit(selectedItems.sort((a, b) => a.order - b.order).map(item => item.data));
    }
    this.cdr.detectChanges();
  }

  removeSelection(data): void {
    this.checkboxCache.filter(item => item.selected && item.data[this.config.fieldId] !== data[this.config.fieldId])
      .forEach(elem => { elem.selected = false, elem.order = -1; });
  }

  selectMultiple(currentIdx): void {
    if (this.lastIdxSelected !== null) {
      while (currentIdx !== this.lastIdxSelected) {
        this.checkboxCache[this.lastIdxSelected].selected = true;
        this.checkboxCache[this.lastIdxSelected].order = this.order++;
        this.lastIdxSelected = currentIdx > this.lastIdxSelected ? this.lastIdxSelected + 1 : this.lastIdxSelected - 1;
      }
    } else {
      this.checkboxCache[currentIdx].selected = true;
      this.checkboxCache[currentIdx].order = this.order++;
    }
    this.lastIdxSelected = currentIdx;
  }

  dblClickRow(data: any): void {
    this.ondlclickRow.emit(data);
  }

  transformDate(date): string {
    const m = moment(date);
    if (m.isValid()) {
      m.locale(this.i18n.getLocale().locale);
      return m.format(this.i18n.getLocale().locale === 'de' ? 'DD.MM.YYYY' : 'MM/DD/YYYY');
    }
    return '';
  }
}

export interface CheckboxSelect {
  data: any;
  selected: boolean | number;
  order?: number;
}
