import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ComponentFactory,
  ComponentFactoryResolver,
  Directive,
  ElementRef,
  EventEmitter,
  Host,
  Input,
  Optional,
  Output,
  Renderer2,
  ViewContainerRef,
  ViewEncapsulation
} from '@angular/core';
import { zoomBigMotion } from 'ng-zorro-antd/core/animation';
import { NzNoAnimationDirective } from 'ng-zorro-antd/core/no-animation';
import { NgStyleInterface, NzTSType } from 'ng-zorro-antd/core/types';

import { Directionality } from '@angular/cdk/bidi';
import { NzTooltipTrigger, NzTooltipBaseDirective, isTooltipEmpty } from '../cmacs-tooltip/base';
import { CmacsToolTipComponent } from '../cmacs-tooltip/tooltip';

@Directive({
  selector: '[cmacs-popover]',
  exportAs: 'cmacsPopover',  
  host: {
    '[class.ant-popover-open]': 'visible'
  }
})
export class CmacsPopoverDirective extends NzTooltipBaseDirective {
  @Input('cmacsPopoverTitle') title?: NzTSType;
  @Input('cmacsPopoverContent') content?: NzTSType;
  @Input('cmacs-popover') directiveTitle?: NzTSType | null;
  @Input('cmacsPopoverTrigger') trigger?: NzTooltipTrigger = 'hover';
  @Input('cmacsPopoverPlacement') placement?: string | string[] = 'top';
  @Input('cmacsPopoverOrigin') origin?: ElementRef<HTMLElement>;
  @Input('cmacsPopoverVisible') visible?: boolean;
  @Input('cmacsPopoverMouseEnterDelay') mouseEnterDelay?: number;
  @Input('cmacsPopoverMouseLeaveDelay') mouseLeaveDelay?: number;
  @Input('cmacsPopoverOverlayClassName') overlayClassName?: string;
  @Input('cmacsPopoverOverlayStyle') overlayStyle?: NgStyleInterface;

  // tslint:disable-next-line:no-output-rename
  @Output('cmacsPopoverVisibleChange') readonly visibleChange = new EventEmitter<boolean>();

  componentFactory: ComponentFactory<CmacsPopoverComponent> = this.resolver.resolveComponentFactory(CmacsPopoverComponent);

  constructor(
    elementRef: ElementRef,
    hostView: ViewContainerRef,
    resolver: ComponentFactoryResolver,
    renderer: Renderer2,
    @Host() @Optional() public noAnimation?: NzNoAnimationDirective
  ) {
    super(elementRef, hostView, resolver, renderer, noAnimation);
  }
}

@Component({
  selector: 'cmacs-popover',
  exportAs: 'CmacsPopoverComponent',
  styleUrls: ['./cmacs-popover.component.css'],
  animations: [zoomBigMotion],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  template: `
    <ng-template
      #overlay="cdkConnectedOverlay"
      cdkConnectedOverlay
      nzConnectedOverlay
      [cdkConnectedOverlayOrigin]="origin"
      [cdkConnectedOverlayPositions]="_positions"
      [cdkConnectedOverlayOpen]="_visible"
      [cdkConnectedOverlayPush]="true"
      (overlayOutsideClick)="onClickOutside($event)"
      (detach)="hide()"
      (positionChange)="onPositionChange($event)"
    >
      <div
        class="ant-popover"
        [class.ant-popover-rtl]="dir === 'rtl'"
        [ngClass]="_classMap"
        [ngStyle]="cmacsOverlayStyle"
        [@.disabled]="noAnimation?.nzNoAnimation"
        [nzNoAnimation]="noAnimation?.nzNoAnimation"
        [@zoomBigMotion]="'active'"
      >
        <div class="ant-popover-content">
          <div class="ant-popover-inner" role="tooltip">
            <div>
              <div class="ant-popover-title" *ngIf="cmacsTitle">
                <ng-container *nzStringTemplateOutlet="cmacsTitle">{{ cmacsTitle }}</ng-container>
              </div>
              <div class="ant-popover-inner-content">
                <ng-container *nzStringTemplateOutlet="cmacsContent">{{ cmacsContent }}</ng-container>
              </div>
            </div>
          </div>
        </div>
      </div>
    </ng-template>
  `
})
export class CmacsPopoverComponent extends CmacsToolTipComponent {
  _prefix = 'ant-popover';

  constructor(
    cdr: ChangeDetectorRef,
    @Optional() directionality: Directionality,
    @Host() @Optional() public noAnimation?: NzNoAnimationDirective
  ) {
    super(cdr, directionality, noAnimation);
  }
  protected isEmpty(): boolean {
    return isTooltipEmpty(this.cmacsTitle) && isTooltipEmpty(this.cmacsContent);
  }
}
